import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IFamilyListingPayload,
	IFamilyListingRequest,
	INotifyNetworkFamilyListingClear,
	INotifyNetworkFamilyListingCompleted,
	INotifyNetworkFamilyListingError,
	INotifyNetworkFamilyListingFailed,
	INotifyNetworkFamilyListingInit,
	INotifyNetworkFamilyListingRequested,
	NETWORK_FAMILY_LISTING_CLEAR,
	NETWORK_FAMILY_LISTING_COMPLETED,
	NETWORK_FAMILY_LISTING_ERROR,
	NETWORK_FAMILY_LISTING_FAILED,
	NETWORK_FAMILY_LISTING_INIT,
	NETWORK_FAMILY_LISTING_REQUESTED
} from './types';

export const notifyNetworkFamilyListingRequested = (data: IFamilyListingRequest = {}): INotifyNetworkFamilyListingRequested => {
	return notifyActionWithPayload(NETWORK_FAMILY_LISTING_REQUESTED, data);
};

export const notifyNetworkFamilyListingInit = (): INotifyNetworkFamilyListingInit => {
	return notifyAction(NETWORK_FAMILY_LISTING_INIT);
};

export const notifyNetworkFamilyListingCompleted = (data: IFamilyListingPayload): INotifyNetworkFamilyListingCompleted => {
	return notifyActionWithPayload(NETWORK_FAMILY_LISTING_COMPLETED, data);
};

export const notifyNetworkFamilyListingFailed = (data: Unsuccessful<IFamilyListingPayload>): INotifyNetworkFamilyListingFailed => {
	return notifyActionWithPayload(NETWORK_FAMILY_LISTING_FAILED, data);
};

export const notifyNetworkFamilyListingError = (): INotifyNetworkFamilyListingError => {
	return notifyAction(NETWORK_FAMILY_LISTING_ERROR);
};

export const notifyNetworkFamilyListingClear = (): INotifyNetworkFamilyListingClear => {
	return notifyAction(NETWORK_FAMILY_LISTING_CLEAR);
};
