import React, { FunctionComponent } from 'react';

interface Props {
	enable?: boolean;
}

const StrictMode: FunctionComponent<Props> = ({ enable, children }) => {

	enable = typeof enable == 'undefined' || enable;

	return !enable
		? (<>{children}</>)
		: (
			<React.StrictMode>
				{children}
			</React.StrictMode>
		);
};

export default StrictMode;
