import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { IMainMemberProfileState, MAIN_MEMBER_SET_PROFILE, MainMemberProfileActions } from './types';

const initialState = null;

const MainMemberProfileReducer: Reducer<IMainMemberProfileState, MainMemberProfileActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IMainMemberProfileState>) => {
		switch (action.type) {
			case MAIN_MEMBER_SET_PROFILE: {

				const { id, userId, caponucleo, caponucleoDettagli } = action.payload;

				return {
					id,
					userId,
					...caponucleo,
					...caponucleoDettagli
				};
			}
		}
	});
};

export default MainMemberProfileReducer;
