import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { FamilyCreateActions, IFamilyCreateState } from './types';
import { apiCompleted, apiError, apiFailed, apiInit, getApiDefaultState } from '../../../../../network/helpers';
import {
	NETWORK_FAMILY_CREATE_CLEAR,
	NETWORK_FAMILY_CREATE_COMPLETED,
	NETWORK_FAMILY_CREATE_ERROR,
	NETWORK_FAMILY_CREATE_FAILED,
	NETWORK_FAMILY_CREATE_INIT
} from '../../../network/family/create/types';
import { ROUTER_HISTORY_CHANGED } from '../../../../../router/routerHistory/store/types';

const initialState = Object.freeze<IFamilyCreateState>({
	api: getApiDefaultState()
});

const FamilyCreateReducer: Reducer<IFamilyCreateState, FamilyCreateActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IFamilyCreateState>) => {
		switch (action.type) {
			case ROUTER_HISTORY_CHANGED: {
				return initialState;
			}
			case NETWORK_FAMILY_CREATE_INIT: {
				apiInit(draft.api);
				break;
			}
			case NETWORK_FAMILY_CREATE_COMPLETED: {

				const { status, message } = action.payload;

				apiCompleted(draft.api, status, message);
				break;
			}
			case NETWORK_FAMILY_CREATE_FAILED: {

				const { status, message, validationErrors } = action.payload;

				apiFailed(draft.api, status, message, validationErrors);
				break;
			}
			case NETWORK_FAMILY_CREATE_ERROR: {
				apiError(draft.api);
				break;
			}
			case NETWORK_FAMILY_CREATE_CLEAR: {
				draft.api = initialState.api;
				break;
			}
		}
	});
};

export default FamilyCreateReducer;
