import React, { FunctionComponent } from 'react';
import style from './style/ActionBox.module.scss';
import ActionButton from '../actionButton';
import ProImage from '../../../shared/ui/proImage';
import { sanitizeHtml } from '../../../../helpers/utils';

interface Props {
	title: string;
	description: string;
	buttonText: string;
	onClick: () => void;
	image: string;
}

const ActionBox: FunctionComponent<Props> = (props) => {
	return (
		<div className={'w-auto'}>
			<div className={style.content}>
				<div className={style.imageContainer}>
					<ProImage src={props.image}/>
				</div>
				<h4 className={style.title}>{props.title}</h4>
				<p
					className={style.description}
					dangerouslySetInnerHTML={{ __html: sanitizeHtml(props.description) }}
				/>
				<ActionButton
					classNames={style.actionButton}
					text={props.buttonText}
					onClick={props.onClick}
				/>
			</div>
		</div>
	);
};

export default ActionBox;
