import { notifyActionWithPayload } from '../../../../../store/helpers';
import {
	INotifyMainMemberFactoryClearForm,
	INotifyMainMemberFactoryRefresh,
	INotifyMainMemberFactoryUpdateForm,
	MAIN_MEMBER_FACTORY_CLEAR_FORM,
	MAIN_MEMBER_FACTORY_REFRESH,
	MAIN_MEMBER_FACTORY_UPDATE_FORM
} from './types';

export const notifyMainMemberFactoryRefresh = (data: INotifyMainMemberFactoryRefresh['payload']): INotifyMainMemberFactoryRefresh => {
	return notifyActionWithPayload(MAIN_MEMBER_FACTORY_REFRESH, data);
};

export const notifyMainMemberFactoryUpdateForm = (fields: INotifyMainMemberFactoryUpdateForm['payload']): INotifyMainMemberFactoryUpdateForm => {
	return notifyActionWithPayload(MAIN_MEMBER_FACTORY_UPDATE_FORM, fields);
};

export const notifyMainMemberFactoryClearForm = (fields: INotifyMainMemberFactoryClearForm['payload'] = {}): INotifyMainMemberFactoryClearForm => {
	return notifyActionWithPayload(MAIN_MEMBER_FACTORY_CLEAR_FORM, fields);
};
