import React, { FunctionComponent } from 'react';
import style from './style/Tag.module.scss';
import { mergeClasses } from '../../../../helpers/utils';

interface Props {
	title: string;
	classnames?: string;
}

const Tag: FunctionComponent<Props> = (props) => {
	return (
		<div className={mergeClasses(style.tag, props.classnames)}>{props.title}</div>
	);
};

export default Tag;
