import { applyMiddleware, CombinedState, legacy_createStore, Middleware, PreloadedState, Store } from 'redux';
import RootReducer, { RootState } from './rootReducer';
import { composeWithDevTools } from 'redux-devtools-extension/developmentOnly';
import SagaManager from '../network/sagaManager';

export type StoreType = Store<CombinedState<RootState>, any> & { dispatch: unknown };

export type StoreManagerConfiguration = {
	preloadedState?: PreloadedState<RootState>;
	logRootState?: boolean;
};

export default class StoreManager {

	private readonly config: StoreManagerConfiguration;
	private readonly sagaManager: SagaManager;
	private readonly store: StoreType;

	constructor(config?: StoreManagerConfiguration) {

		this.config = config || {};

		// Redux DevTools Configuration
		const composeEnhancers = composeWithDevTools({
			name: 'ArmyCard@RequestModule',
			maxAge: 100,
			latency: 0,
			shouldRecordChanges: true,
			shouldHotReload: false
		});

		// Redux Saga Configuration
		this.sagaManager = new SagaManager();

		// Initializing Redux Store
		this.store = legacy_createStore(
			RootReducer,
			config?.preloadedState,
			composeEnhancers(
				applyMiddleware(...this.getMiddlewares())
			)
		);

		// Starting Redux Saga
		this.sagaManager.run();

		if (config?.logRootState) {
			console.log(`Root State: ${JSON.stringify(this.getState())}`);
		}
	}

	getStore = () => this.store;

	getState = () => this.getStore().getState();

	dispatch = (action: any) => this.getStore().dispatch(action);

	getSagaManager = () => this.sagaManager;

	close = () => this.dispatch(this.sagaManager.getEndAction());

	private getMiddlewares = (): Middleware[] => {
		return ([
			this.sagaManager.getMiddleware()
		].filter(Boolean) as Middleware[]);
	};
}
