import React, { FunctionComponent, useState } from 'react';
import style from './style/FamilyMemberModalForm.module.scss';
import Modal from '../../../shared/utilities/modal';
import { UnwrapElementCallback } from '../../../../helpers/utils';
import ProImage from '../../../shared/ui/proImage';
import { Close } from '../../../../assets/images/close/close';
import { useTranslation } from 'react-i18next';
import MessageBox from '../messageBox';
import InputRadio from '../../../shared/ui/forms/input-radio';
import Input from '../../../shared/ui/forms/input';
import InputSelect from '../../../shared/ui/forms/input-select';
import ActionButton from '../actionButton';
import { IMemberFormStoreProps } from './store/props';
import DocumentPicker from '../documentPicker';
import PassportPhotoPicker from '../passportPhotoPicker';
import { DateTime } from 'luxon';
import { IMember } from '../../models/IMember';
import { useMount, useMountNext } from '../../../system/hooks';
import { isRequestSuccessful } from '../../../../network/utils';
import { IFormFile } from '../../../../store/models/IFormFile';
import { IFormPicture } from '../../../../store/models/IFormPicture';
import Validator from '../../../../helpers/validator';

interface Props extends IMemberFormStoreProps {
	mode: 'CREATE' | 'EDIT';
	familyId: number | null;
	member?: IMember;
	onDismiss: () => void;
}

const RELATIONSHIP: { key: string; value: string; }[] = [
	{ key: 'wife', value: 'Moglie' },
	{ key: 'son', value: 'Figlio' },
	{ key: 'daughter', value: 'Figlia' },
	{ key: 'father', value: 'Padre' },
	{ key: 'mother', value: 'Madre' }
];

const FamilyMemberModalForm: FunctionComponent<Props> = (props) => {

	const { create, update, member, profile } = props;

	const [ formData, setFormData ] = useState<{
		name: string;
		surname: string;
		fiscalCode: string;
		birthPlace: string;
		birthDate: string;
		nationality: string;
		primaryEmail: string;
		primaryPhone: string;
		relationship: string;
		isDefenseDepartmentEmployee: boolean;
		identityDocument: IFormFile;
		fiscalCodeDocument: IFormFile;
		passportPhotoDocument: IFormPicture;
	}>({
		name: '',
		surname: '',
		fiscalCode: '',
		birthPlace: '',
		birthDate: '',
		nationality: '',
		primaryEmail: '',
		primaryPhone: '',
		relationship: '',
		isDefenseDepartmentEmployee: false,
		identityDocument: {
			data: '',
			name: '',
			mimeType: '',
			extension: '',
			isAttached: false
		},
		fiscalCodeDocument: {
			data: '',
			name: '',
			mimeType: '',
			extension: '',
			isAttached: false
		},
		passportPhotoDocument: {
			data: '',
			name: '',
			mimeType: '',
			extension: '',
			isAttached: false
		}
	});

	const { t } = useTranslation();

	const onSubmit = () => {
		if (create.api.isLoading) return;
		if (props.mode === 'CREATE') {
			props.notifyNetworkMemberCreateRequested({
				data: {
					nucleoId: props.familyId ?? null,
					name: formData.name,
					surname: formData.surname,
					birthDate: formData.birthDate,
					birthPlace: formData.birthPlace,
					fiscalCode: formData.fiscalCode,
					nationality: formData.nationality,
					primaryEmail: formData.primaryEmail,
					primaryPhone: formData.primaryPhone,
					relationship: formData.relationship,
					isDefenseDepartmentEmployee: formData.isDefenseDepartmentEmployee,
					identityDocument: formData.identityDocument.data,
					fiscalCodeDocument: formData.fiscalCodeDocument.data,
					passportPhotoDocument: formData.passportPhotoDocument.data
				}
			});
		} else {
			props.notifyNetworkMemberEditRequested({
				configuration: {
					params: [ props.member!.id ]
				},
				data: {
					nucleoId: props.familyId ?? null,
					name: formData.name,
					surname: formData.surname,
					birthDate: formData.birthDate,
					birthPlace: formData.birthPlace,
					fiscalCode: formData.fiscalCode,
					nationality: formData.nationality,
					primaryEmail: formData.primaryEmail,
					primaryPhone: formData.primaryPhone,
					relationship: formData.relationship,
					isDefenseDepartmentEmployee: formData.isDefenseDepartmentEmployee,
					identityDocument: formData.identityDocument.data,
					fiscalCodeDocument: formData.fiscalCodeDocument.data,
					passportPhotoDocument: formData.passportPhotoDocument.data
				}
			});
		}
	};

	const onDismiss = () => {
		props.notifyNetworkMemberCreateClear();
		props.notifyNetworkMemberEditClear();
		props.onDismiss();
	};

	useMount(() => {
		if (!member) return;
		setFormData({
			...formData,
			name: member.name,
			surname: member.surname,
			fiscalCode: member.fiscalCode,
			birthPlace: member.birthPlace,
			birthDate: member.birthDate,
			nationality: member.nationality,
			primaryPhone: member.primaryPhone,
			primaryEmail: member.primaryEmail,
			relationship: member.relationship,
		});
	}, [ create, member ]);

	useMountNext(() => {
		if (isRequestSuccessful(create.api) || isRequestSuccessful(update.api)) {
			onDismiss();
		}
	}, [ create.api.receivedAt, update.api.receivedAt ]);

	return (
		<Modal className={style.modal} onRequestClose={onDismiss}>
			<div className={style.container}>

				<div className={'w-100 position__relative'}>
					<h4 className={style.title}>Aggiungi membro a nucleo familiare</h4>
					<div className={style.closeButton} onClick={UnwrapElementCallback(onDismiss)}>
						<ProImage src={Close}/>
					</div>
				</div>

				<p>{t('Inserisci le informazioni relative al familiare che vuoi aggiungere al tuo nucleo familiare.')}</p>

				<MessageBox
					message={t(`Stai aggiungendo un familiare al nucleo familiare principale. Il capo Nucleo è il ${profile!.grade} ${profile!.name} ${profile!.surname}.`)}
					type={'info'}
				/>

				<form
					className={'w-100'}
					onSubmit={(e) => e.preventDefault()}
					onSubmitCapture={(e) => e.preventDefault()}
				>
					<div className={style.formSection}>
						<InputRadio
							name={'defense_member'}
							optionClassnames={style.employeeOption}
							labelClassnames={style.employeeOptionLabel}
							value={formData.isDefenseDepartmentEmployee ? 'defense_employee' : 'not_defense_employee'}
							options={[
								{
									value: 'not_defense_employee',
									label: t('Il membro non è un dipendente dell’Amministrazione Difesa')
								},
								{
									value: 'defense_employee',
									label: t('Il membro è un dipendente dell’Amministrazione Difesa')
								}
							]}
							onChange={({ value }) => setFormData({
								...formData,
								isDefenseDepartmentEmployee: value === 'defense_employee'
							})}
						/>
					</div>

					<div className={style.formSection}>

						<h5 className={'mb-half'}>{t('Dati anagrafici')}</h5>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'name'}
									label={t('Nome')}
									value={formData.name}
									onChange={(e) => setFormData({
										...formData,
										name: e.target.value
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'surname'}
									label={t('Cognome')}
									value={formData.surname}
									onChange={(e) => setFormData({
										...formData,
										surname: e.target.value
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'fiscal_code'}
									label={t('Codice Fiscale')}
									value={formData.fiscalCode}
									onChange={(e) => setFormData({
										...formData,
										fiscalCode: e.target.value
									})}
									validate={Validator.fiscalCode}
								/>
							</div>
						</div>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'birth_place'}
									label={t('Luogo di nascita')}
									value={formData.birthPlace}
									onChange={(e) => setFormData({
										...formData,
										birthPlace: e.target.value
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'birth_date'}
									label={t('Data di nascita')}
									type={'date'}
									value={DateTime.fromISO(formData.birthDate).toFormat('yyyy-MM-dd')}
									onChange={(e) => setFormData({
										...formData,
										birthDate: DateTime.fromISO(e.target.value).toFormat('yyyy-MM-dd')
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'nationality'}
									label={t('Nazionalità')}
									value={formData.nationality}
									onChange={(e) => setFormData({
										...formData,
										nationality: e.target.value
									})}
								/>
							</div>
						</div>

					</div>

					<div className={style.formSection}>

						<h5 className={'mb-half'}>{t('Contatti')}</h5>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'primary_phone'}
									label={t('Telefono')}
									value={formData.primaryPhone}
									onChange={(e) => setFormData({
										...formData,
										primaryPhone: e.target.value
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'primary_email'}
									type={'email'}
									label={t('Mail')}
									value={formData.primaryEmail}
									onChange={(e) => setFormData({
										...formData,
										primaryEmail: e.target.value
									})}
								/>
							</div>
						</div>

					</div>

					<div className={style.formSection}>

						<h5 className={'mb-half'}>{t('Rapporto di parentela')}</h5>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<InputSelect
									name={'relationship'}
									label={t('Rapporto parentela')}
									placeholder={t('Rapporto Parentela con Capo Nucleo')}
									value={formData.relationship}
									defaultValue={RELATIONSHIP[0].key}
									options={RELATIONSHIP.map(({ key, value }) => ({ label: value, value: key }))}
									searchable={false}
									onSelect={(option) => setFormData({
										...formData,
										relationship: option?.value ?? ''
									})}
								/>
							</div>
						</div>

					</div>

					<div className={style.formSection}>

						<h4 className={'mb-half'}>{t('Carica i documenti')}</h4>

						<div className={'w-100 mt-1'}>
							<DocumentPicker
								title={t('Documento di identità')}
								subtitle={t('(peso massimo: 8Mb; formato: pdf)')}
								document={formData.identityDocument}
								onFilePicked={(document) => setFormData({
									...formData,
									identityDocument: document
								})}
								onClear={() => setFormData({
									...formData,
									identityDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

						<div className={'w-100 mt-1'}>
							<DocumentPicker
								title={t('Codice fiscale')}
								subtitle={t('(peso massimo: 8Mb; formato: pdf)')}
								document={formData.fiscalCodeDocument}
								onFilePicked={(document) => setFormData({
									...formData,
									fiscalCodeDocument: document
								})}
								onClear={() => setFormData({
									...formData,
									fiscalCodeDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

						<div className={'w-100 mt-1'}>
							<PassportPhotoPicker
								photo={formData.passportPhotoDocument}
								onPhotoPicked={(document) => setFormData({
									...formData,
									passportPhotoDocument: document
								})}
								onClear={() => setFormData({
									...formData,
									passportPhotoDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

					</div>

					<div className={style.footer}>
						<ActionButton
							classNames={style.cancel}
							text={t('Annulla')}
							onClick={onDismiss}
						/>
						<ActionButton
							classNames={style.confirm}
							text={props.mode === 'CREATE' ? t('Aggiungi membro') : t('Salva')}
							isLoading={create.api.isLoading}
							onClick={onSubmit}
						/>
					</div>

				</form>
			</div>
		</Modal>
	);
};

export default FamilyMemberModalForm;
