import { combineReducers } from 'redux';
import MainMemberFactoryReducer from './store/mainMember/factory/reducer';
import MainMemberPushFactoryReducer from './store/mainMember/pushFactory/reducer';
import MemberCreateReducer from './store/member/create/reducer';
import FamilyCreateReducer from './store/family/create/reducer';
import FamilyListingReducer from './store/family/listing/reducer';
import MemberEditReducer from './store/member/edit/reducer';
import MainMemberProfileReducer from './store/mainMember/profile/reducer';

const CardRequestModuleReducer = combineReducers({
	family: combineReducers({
		create: FamilyCreateReducer,
		listing: FamilyListingReducer
	}),
	mainMember: combineReducers({
		factory: MainMemberFactoryReducer,
		profile: MainMemberProfileReducer,
		update: MainMemberPushFactoryReducer
	}),
	member: combineReducers({
		create: MemberCreateReducer,
		edit: MemberEditReducer
	})
});

export default CardRequestModuleReducer;
