import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { IMemberEditState, MemberEditActions } from './types';
import { apiCompleted, apiError, apiFailed, apiInit, getApiDefaultState } from '../../../../../network/helpers';
import {
	NETWORK_MEMBER_EDIT_CLEAR,
	NETWORK_MEMBER_EDIT_COMPLETED,
	NETWORK_MEMBER_EDIT_ERROR,
	NETWORK_MEMBER_EDIT_FAILED,
	NETWORK_MEMBER_EDIT_INIT
} from '../../../network/member/edit/types';
import { ROUTER_HISTORY_CHANGED } from '../../../../../router/routerHistory/store/types';

const initialState = Object.freeze<IMemberEditState>({
	api: getApiDefaultState()
});

const MemberEditReducer: Reducer<IMemberEditState, MemberEditActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IMemberEditState>) => {
		switch (action.type) {
			case ROUTER_HISTORY_CHANGED: {
				return initialState;
			}
			case NETWORK_MEMBER_EDIT_INIT: {
				apiInit(draft.api);
				break;
			}
			case NETWORK_MEMBER_EDIT_COMPLETED: {

				const { status, message } = action.payload;

				apiCompleted(draft.api, status, message);
				break;
			}
			case NETWORK_MEMBER_EDIT_FAILED: {

				const { status, message, validationErrors } = action.payload;

				apiFailed(draft.api, status, message, validationErrors);
				break;
			}
			case NETWORK_MEMBER_EDIT_ERROR: {
				apiError(draft.api);
				break;
			}
			case NETWORK_MEMBER_EDIT_CLEAR: {
				draft.api = initialState.api;
				break;
			}
		}
	});
};

export default MemberEditReducer;
