import { DEVELOPMENT_MODE } from '../constants';
import _sanitizeHtml from 'sanitize-html';
import { matchPath } from 'react-router-dom';
import classNames, { Argument } from 'classnames';
import { DateTime } from 'luxon';
import { ElementEvent } from '../types/elementEvent';
import Resizer from 'react-image-file-resizer';
import { base64StringToBlob, blobToBase64String } from 'blob-util';

export const MockCallback = (() => {});

export const UnwrapElementCallback = (callback: (e: ElementEvent) => void) => (e: ElementEvent): void => {
	e.stopPropagation();
	callback(e);
};

export const UnwrapElementDataCallback = <T extends { [K in Attrs[number]]: string | undefined }, Attrs extends readonly string[]>(callback: (e: ElementEvent, data: T) => void, ...attributes: Attrs) => (e: ElementEvent): void => {
	e.stopPropagation();
	const data: { [key: string]: string | undefined } = {};
	for (const attribute of attributes) {
		data[attribute] = e.currentTarget.getAttribute(`data-${attribute}`);
	}
	callback(e, data as T);
};

export const canUseDOM = ((): () => boolean => {
	const canUseDom = typeof window != 'undefined' && typeof window.document != 'undefined' && typeof window.document.createElement != 'undefined';
	return () => canUseDom;
})();

export const isContextSecure = (): boolean => {
	return canUseDOM() && window.isSecureContext;
};

export const localStorageSetData = (key: string, value: any): boolean => {
	if (!canUseDOM() || typeof localStorage == 'undefined') return false;
	if (typeof value === 'object') {
		localStorage.setItem(key, JSON.stringify(value));
	} else {
		localStorage.setItem(key, value);
	}
	return true;
};

export const localStorageGetData = <T>(key: string, deserialize: boolean = false, defaultValue: T | undefined = undefined): T | string | undefined => {
	if (!canUseDOM() || typeof localStorage == 'undefined') return undefined;
	try {
		const data = localStorage.getItem(key);
		if (data) {
			if (deserialize) {
				try {
					return JSON.parse(data) as T;
				} catch (e) {
					localStorageRemoveData(key);
					return defaultValue;
				}
			}
			return data;
		}
		return defaultValue;
	} catch (e) {
		console.error(e);
		return defaultValue;
	}
};

export const localStorageRemoveData = (key: string): boolean => {
	if (!canUseDOM() || typeof localStorage == 'undefined') return false;
	localStorage.removeItem(key);
	return true;
};

export const getBrowserLanguageCode = (): string | undefined => {
	const code = navigator?.language;
	return typeof code != 'undefined' ? code : undefined;
};

export const getBrowserTimezone = (): string | undefined => {
	return canUseDOM() ? Intl.DateTimeFormat().resolvedOptions().timeZone : undefined;
};

export const getValueFromWindow = <T>(prop: string, defaultValue: T, remove: boolean = false): T => {
	if (canUseDOM()) {
		const value: T = (window as any)[prop] as T;
		if (value) {
			if (remove) {
				delete (window as any)[prop];
			}
			return value;
		}
	}
	return defaultValue;
};

export const delay = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

export const isEmpty = (value: string | undefined | null | false): boolean => {
	if (typeof value != 'string') return true;
	return !value || !value.trim() || value.length === 0;
};

export const getUnixMillsTimestamp = (): number => {
	return DateTime.now().toMillis();
};

export const scrollToTop = (smooth: boolean = false): void => {
	if (!canUseDOM()) return;
	if (smooth) {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	} else {
		window.scrollTo(0, 0);
	}
};

export const copyToClipboard = async (data: string): Promise<void> => {
	try {
		if (!isContextSecure()) {
			console.warn('Cannot copy to clipboard.');
			return;
		}
		await navigator.clipboard.writeText(data);
	} catch (e) {
		console.error(e);
	}
};

export const matchRoute = (pattern: string, path: string): boolean => {
	return !!matchPath(pattern, path);
};

export const mergeClasses = (...classes: Argument[]): string => {
	return classNames(classes);
};

export const urlEncode = (data: string, enhanced: boolean = false): string => {
	const encoded = encodeURIComponent(data);
	if (enhanced) {
		return encoded
			.replace(/-/g, '%2D')
			.replace(/_/g, '%5F')
			.replace(/\./g, '%2E')
			.replace(/!/g, '%21')
			.replace(/~/g, '%7E')
			.replace(/\*/g, '%2A')
			.replace(/'/g, '%27')
			.replace(/\(/g, '%28')
			.replace(/\)/g, '%29');
	}
	return encoded;
};

export const urlDecode = (data: string): string => {
	try {
		const normalizedData = data
			.replace(/%2D/g, '-')
			.replace(/%5F/g, '_')
			.replace(/%2E/g, '.')
			.replace(/%21/g, '!')
			.replace(/%7E/g, '~')
			.replace(/%2A/g, '*')
			.replace(/%27/g, '\'')
			.replace(/%28/g, '(')
			.replace(/%29/g, ')');
		return decodeURIComponent(normalizedData);
	} catch {
		return '';
	}
};

export const sanitizeHtml = (html: string): string => {
	return _sanitizeHtml(html, {
		allowedTags: [
			'p', 'span', 'strong', 'em', 'u',
			'ul', 'ol', 'li', 'a', 'div'
		],
		allowedAttributes: {
			'*': [ 'style' ],
			'a': [
				'title',
				'href',
				'rel',
				'target'
			]
		},
		allowedStyles: {
			'*': {
				'width': [ /^\d+(?:px|em|rem|%)$/ ],
				'height': [ /^\d+(?:px|em|rem|%)$/ ],
				'font-size': [ /^\d+(?:px|em|rem|%)$/ ],
				'margin-left': [ /^\d+(?:px|em|rem|%)$/ ],
				'margin-right': [ /^\d+(?:px|em|rem|%)$/ ],
				'color': [
					/^#(0x)?[0-9a-f]+$/i,
					/^rgb\(\s*(\d{1,3})\s*,\s*(\d{1,3})\s*,\s*(\d{1,3})\s*\)$/
				],
				'vertical-align': [
					/^baseline$/,
					/^length$/,
					/^sub$/,
					/^super$/,
					/^top$/,
					/^text-top$/,
					/^middle$/,
					/^bottom$/,
					/^text-bottom$/
				],
				'text-align': [
					/^left$/,
					/^center$/,
					/^right$/,
					/^justify$/
				],
				'list-style-type': [
					/^circle$/,
					/^decimal$/,
					/^disc$/,
					/^square$/,
					/^upper-alpha$/,
					/^lower-alpha$/,
					/^upper-greek$/,
					/^lower-greek$/,
					/^upper-latin$/,
					/^lower-latin$/,
					/^upper-roman$/,
					/^lower-roman$/
				]
			}
		}
	});
};

export const stripTags = (html: string): string => {
	return _sanitizeHtml(html, {
		allowedTags: [],
		allowedAttributes: {}
	});
};

export const withHttp = (domain: string): string => {
	return withProtocol('http://', domain);
};

export const withHttps = (domain: string, force: boolean = false): string => {
	if (!force && DEVELOPMENT_MODE) {
		return withHttp(domain);
	}
	return withProtocol('https://', domain);
};

export const withProtocol = (protocol: string, domain: string): string => {
	if (!isEmpty(domain)) {
		return `${protocol}${(domain.startsWith('/') ? domain.substring(1) : domain)}`;
	}
	return protocol;
};

export const optimizeImage = <T extends string | Blob | File>(
	file: File,
	outputType: 'base64' | 'blob',
	extension: string,
	quality: number = 100,
	maxWidth: number = 1200,
	maxHeight: number = 1200
) => {
	return new Promise<T>(resolve => {
		Resizer.imageFileResizer(
			file,
			maxWidth,
			maxHeight,
			extension === 'png' ? 'PNG' : extension === 'webp' ? 'WEBP' : 'JPEG',
			quality,
			0,
			(data) => resolve(data as T),
			outputType,
			192,
			192
		);
	});
};

export const convertBase64ToBlob = (image: string, mimeType: string): Blob => {
	return base64StringToBlob(image.split(',')[1], mimeType);
};

export const convertBlobToBase64 = async (blob: Blob): Promise<string> => {
	try {
		return await blobToBase64String(blob);
	} catch (e) {
		console.log(e);
	}
	return '';
};
