import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import {
	IMainMemberFactoryRequest,
	IMainMemberFactoryResponse,
	INotifyNetworkMainMemberFactoryRequested,
	NETWORK_MAIN_MEMBER_FACTORY_REQUESTED
} from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkMainMemberFactoryCompleted,
	notifyNetworkMainMemberFactoryError,
	notifyNetworkMainMemberFactoryFailed,
	notifyNetworkMainMemberFactoryInit
} from './actions';
import delay from '../../../../../network/effects/delay';
import { notifyMainMemberSetProfile } from '../../../store/mainMember/profile/actions';

const ENDPOINT = 'form';

const api = ({ configuration }: IMainMemberFactoryRequest) => {
	return ConnectionCore.getInstance().get<IMainMemberFactoryResponse>(ENDPOINT);
};

function* entity(action: INotifyNetworkMainMemberFactoryRequested) {
	try {

		yield put(notifyNetworkMainMemberFactoryInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IMainMemberFactoryResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {

			const data = response.data.data;

			yield all([
				put(notifyNetworkMainMemberFactoryCompleted({
					status: response.status,
					message: response.data.message,
					data
				})),
				put(notifyMainMemberSetProfile(data))
			]);
		} else {
			yield put(notifyNetworkMainMemberFactoryFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkMainMemberFactoryError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_MAIN_MEMBER_FACTORY_REQUESTED, entity);
}

export default fork(watcher);
