import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IFamilyCreatePayload,
	IFamilyCreateRequest,
	INotifyNetworkFamilyCreateClear,
	INotifyNetworkFamilyCreateCompleted,
	INotifyNetworkFamilyCreateError,
	INotifyNetworkFamilyCreateFailed,
	INotifyNetworkFamilyCreateInit,
	INotifyNetworkFamilyCreateRequested,
	NETWORK_FAMILY_CREATE_CLEAR,
	NETWORK_FAMILY_CREATE_COMPLETED,
	NETWORK_FAMILY_CREATE_ERROR,
	NETWORK_FAMILY_CREATE_FAILED,
	NETWORK_FAMILY_CREATE_INIT,
	NETWORK_FAMILY_CREATE_REQUESTED
} from './types';

export const notifyNetworkFamilyCreateRequested = (data: IFamilyCreateRequest = {}): INotifyNetworkFamilyCreateRequested => {
	return notifyActionWithPayload(NETWORK_FAMILY_CREATE_REQUESTED, data);
};

export const notifyNetworkFamilyCreateInit = (): INotifyNetworkFamilyCreateInit => {
	return notifyAction(NETWORK_FAMILY_CREATE_INIT);
};

export const notifyNetworkFamilyCreateCompleted = (data: IFamilyCreatePayload): INotifyNetworkFamilyCreateCompleted => {
	return notifyActionWithPayload(NETWORK_FAMILY_CREATE_COMPLETED, data);
};

export const notifyNetworkFamilyCreateFailed = (data: Unsuccessful<IFamilyCreatePayload>): INotifyNetworkFamilyCreateFailed => {
	return notifyActionWithPayload(NETWORK_FAMILY_CREATE_FAILED, data);
};

export const notifyNetworkFamilyCreateError = (): INotifyNetworkFamilyCreateError => {
	return notifyAction(NETWORK_FAMILY_CREATE_ERROR);
};

export const notifyNetworkFamilyCreateClear = (): INotifyNetworkFamilyCreateClear => {
	return notifyAction(NETWORK_FAMILY_CREATE_CLEAR);
};
