import React, { DependencyList, EffectCallback, MutableRefObject, useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { canUseDOM, matchRoute } from '../../helpers/utils';
import { NullableRef } from '../../types/nullableRef';
import { NavigateOptions, useLocation, useNavigate } from 'react-router-dom';
import { To } from 'history';
import { ElementEvent } from '../../types/elementEvent';
import { IApiState } from '../../network/models/IApiState';
import { isDefaultStatusCode, isRequestSuccessful, isRequestUnsuccessful } from '../../network/utils';
import _ from 'lodash';
import { getNotFoundRoute } from '../../router/routeComposers';

export const useSwitch = <Hook extends (...args: any[]) => any>(enable: boolean, hook: Hook, ...args: Parameters<Hook>): ReturnType<Hook> => {
	return enable ? hook(...args) : undefined;
};

export const useDOM = <Hook extends (...args: any[]) => any>(hook: Hook, ...args: Parameters<Hook>): ReturnType<Hook> => {
	return useSwitch(canUseDOM(), hook, ...args);
};

export const useMount = (callback: EffectCallback, deps?: DependencyList) => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useEffect(() => callback(), deps);
};

export const useMountOnce = (callback: EffectCallback) => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useEffect(() => callback(), []);
};

export const useMountNext = (callback: EffectCallback, deps?: DependencyList) => {
	const firstTime = useRef(true);
	/* eslint-disable react-hooks/exhaustive-deps */
	useEffect(() => {
		if (firstTime.current) {
			firstTime.current = false;
			return;
		}
		return callback();
	}, deps);
};

export const useLayoutMount = (callback: EffectCallback, deps?: DependencyList) => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useLayoutEffect(() => callback(), deps);
};

export const useLayoutMountOnce = (callback: EffectCallback, deps?: DependencyList) => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useLayoutEffect(() => callback(), []);
};

export const useImmediateMount = (callback: () => void) => {
	callback();
};

export const useUnmount = (callback: () => void, deps?: DependencyList) => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useEffect(() => () => callback(), deps);
};

export const usePreload = <T extends IApiState['api']>(apis: [ T, Function ][]): { successful: boolean; failed: boolean; loading: boolean; } => {
	/* eslint-disable react-hooks/exhaustive-deps */
	useEffect(() => apis.forEach((api) => !api[0].isLoading && isDefaultStatusCode(api[0].status) && api[1]()), []);
	return {
		successful: _.every(apis, (api) => isRequestSuccessful(api[0])),
		failed: _.some(apis, (api) => isRequestUnsuccessful(api[0])),
		loading: _.some(apis, (api) => api[0].isLoading)
	};
};

export const useNullableRef = <T, >() => {
	return useRef<NullableRef<T>>(null);
};

export const useWindowEvent = <T extends keyof WindowEventMap>(key: T, listener: (this: Window, ev: WindowEventMap[T]) => any) => {
	useDOM(useEffect, () => {
		window.addEventListener(key, listener);
		return () => window.removeEventListener(key, listener);
	}, [ listener ]);
};

export const useTimeout = (callback: () => void, delay: number) => {
	const savedCallback = useRef<() => void>();
	useEffect(() => {
		savedCallback.current = callback;
	}, [ callback ]);
	useEffect(() => {
		const tick = () => {
			savedCallback.current && savedCallback.current();
		};
		if (delay) {
			const id = setTimeout(tick, delay);
			return () => clearTimeout(id);
		}
	}, [ delay ]);
};

export const useInterval = (callback: () => void, delay: number) => {
	const savedCallback = useRef<() => void>();
	useEffect(() => {
		savedCallback.current = callback;
	}, [ callback ]);
	useEffect(() => {
		const tick = () => {
			savedCallback.current && savedCallback.current();
		};
		if (delay) {
			const id = setInterval(tick, delay);
			return () => clearInterval(id);
		}
	}, [ delay ]);
};

export const usePrevious = <T, >(value: T, defaultValue?: T): T | undefined => {
	const ref = useRef<T | undefined>(defaultValue);
	useEffect(() => {
		ref.current = value;
	}, [ value ]);
	return ref.current;
};

export const useOnClickOutside = <T extends HTMLElement>(ref: MutableRefObject<T | null>, callback: () => void, deps?: DependencyList) => {
	useDOM(useEffect, () => {
		const handler = (e: ElementEvent) => {
			if (!ref.current || ref.current.contains(e.target)) return;
			callback();
		};
		document.addEventListener('click', handler);
		return () => document.removeEventListener('click', handler);
	}, deps);
};

export const useStrictNavigate = (): { (to?: To, pattern?: string | null, options?: NavigateOptions): boolean; (delta: number): boolean; } => {
	const location = useLocation();
	const navigate = useNavigate();
	return (to?: To | number, pattern?: string | null, options?: NavigateOptions): boolean => {
		if (typeof to == 'undefined') return false;
		if (typeof to == 'number') {
			navigate(to);
		} else {
			if (to === location.pathname) return false;
			if (pattern && matchRoute(pattern, location.pathname)) return false;
			navigate(to, { replace: location.pathname === getNotFoundRoute(), ...options });
		}
		return true;
	};
};

export const useQuery = () => {
	const { search } = useLocation();
	return useMemo(() => new URLSearchParams(search), [ search ]);
};

export const useForceUpdate = () => {
	const [ _, setState ] = useState({});
	return useCallback(() => setState({}), []);
};

export const useWindowSizeObserver = (callback: (size: { width: number, height: number }) => void) => {
	useWindowEvent('resize', () => callback({
		width: window.innerWidth,
		height: window.innerHeight
	}));
};

export const useFormKeyboardBindings = (callbacks: { onSave?: () => void; onSaveAndExit?: () => void; }): void => {
	useWindowEvent('keydown', (event) => {
		if ((event.ctrlKey || event.metaKey) && event.key.toLowerCase() === 's') {
			if (!callbacks.onSave) return;
			event.preventDefault();
			callbacks.onSave();
		} else if ((event.ctrlKey || event.metaKey) && event.key.toLowerCase() === 'enter') {
			if (!callbacks.onSaveAndExit) return;
			event.preventDefault();
			callbacks.onSaveAndExit();
		}
	});
};
