import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import { IMemberEditRequest, IMemberEditResponse, INotifyNetworkMemberEditRequested, NETWORK_MEMBER_EDIT_REQUESTED } from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkMemberEditCompleted,
	notifyNetworkMemberEditError,
	notifyNetworkMemberEditFailed,
	notifyNetworkMemberEditInit
} from './actions';
import delay from '../../../../../network/effects/delay';
import { notifyNetworkFamilyListingRequested } from '../../family/listing/actions';
import { sprintf } from 'sprintf-js';

const ENDPOINT = '/form/nucleo/membro/%d';

const api = ({ data, configuration }: IMemberEditRequest) => {
	return ConnectionCore.getInstance().put<IMemberEditResponse>(sprintf(ENDPOINT, configuration?.params ?? []), data, {
		headers: configuration?.headers
	});
};

function* entity(action: INotifyNetworkMemberEditRequested) {
	try {

		yield put(notifyNetworkMemberEditInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IMemberEditResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {
			yield all([
				put(notifyNetworkMemberEditCompleted({
					status: response.status,
					message: response.data.message
				})),
				put(notifyNetworkFamilyListingRequested())
			]);
		} else {
			yield put(notifyNetworkMemberEditFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkMemberEditError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_MEMBER_EDIT_REQUESTED, entity);
}

export default fork(watcher);
