import { call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import {
	IMainMemberPushFactoryRequest,
	IMainMemberPushFactoryResponse,
	INotifyNetworkMainMemberPushFactoryRequested,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED
} from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkMainMemberPushFactoryCompleted,
	notifyNetworkMainMemberPushFactoryError,
	notifyNetworkMainMemberPushFactoryFailed,
	notifyNetworkMainMemberPushFactoryInit
} from './actions';
import delay from '../../../../../network/effects/delay';

const ENDPOINT = 'form/caponucleo';

const api = ({ data, configuration }: IMainMemberPushFactoryRequest) => {
	return ConnectionCore.getInstance().post<IMainMemberPushFactoryResponse>(ENDPOINT, data, {
		headers: configuration?.headers,
		params: configuration?.query
	});
};

function* entity(action: INotifyNetworkMainMemberPushFactoryRequested) {
	try {

		yield put(notifyNetworkMainMemberPushFactoryInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IMainMemberPushFactoryResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {
			yield put(notifyNetworkMainMemberPushFactoryCompleted({
				status: response.status,
				message: response.data.message
			}));
		} else {
			yield put(notifyNetworkMainMemberPushFactoryFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkMainMemberPushFactoryError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED, entity);
}

export default fork(watcher);
