import React from 'react';
import { optimizeImage } from '../../../../helpers/utils';
import FilePicker from '../filePicker';
import { ImagePicked } from '../../../../types/imagePicked';
import { Unit } from 'bytes';

interface Props {
	maxSize?: number | `${number}${Unit}`;
	onImagePicked: (data: ImagePicked) => void;
	onImageRejected?: () => void;
}

const ImagePicker = React.forwardRef<HTMLInputElement, Props>((props, ref) => {
	return (
		<FilePicker
			ref={ref}
			mimeTypes={'image/jpg, image/jpeg, image/png'}
			maxSize={props.maxSize ?? '5MB'}
			onFilePicked={async (file, mimeType, extension) => {
				try {
					const image = await optimizeImage<string>(file, 'base64', extension);
					props.onImagePicked({ file, image, mimeType, extension });
				} catch (e) {
					console.error(e);
				}
			}}
			onFileRejected={props.onImageRejected}
		/>
	);
});

export default ImagePicker;
