import React, { FunctionComponent, useState } from 'react';
import style from './style/FamilyMembers.module.scss';
import ResponsiveContainer from '../../../shared/ui/responsiveContainer';
import Content from '../../../shared/ui/content';
import { useTranslation } from 'react-i18next';
import ProImage from '../../../shared/ui/proImage';
import { Family } from '../../../../assets/images/family';
import { mergeClasses, UnwrapElementCallback } from '../../../../helpers/utils';
import { ExternalFamily } from '../../../../assets/images/externalFamily';
import { PlusCircle } from '../../../../assets/images/plusCircle';
import { Edit } from '../../../../assets/images/edit';
import Tag from '../../ui/tag';
import ActionButton from '../../ui/actionButton';
import FamilyMemberModalForm from '../../ui/familyMemberModalForm';
import { IFamilyMembersStoreProps } from './store/props';
import { usePreload, useStrictNavigate } from '../../../system/hooks';
import { isRequestFreshOrPending } from '../../../../network/utils';
import { IMember } from '../../models/IMember';
import _ from 'lodash';
import MessageBox from '../../ui/messageBox';
import { getCardRequestFormRoute, getCheckoutRoute } from '../../CardRequestModuleRouteComposers';
import { IMainMember } from '../../models/IMainMember';
import Stepper from '../../ui/stepper';

interface Props extends IFamilyMembersStoreProps {}

const RELATIONSHIP: { key: string; value: string; }[] = [
	{ key: 'wife', value: 'Moglie' },
	{ key: 'son', value: 'Figlio' },
	{ key: 'daughter', value: 'Figlia' },
	{ key: 'father', value: 'Padre' },
	{ key: 'mother', value: 'Madre' }
];

const FamilyMembers: FunctionComponent<Props> = (props) => {

	const { create, listing, profile, factory } = props;

	const { t } = useTranslation();
	const navigate = useStrictNavigate();

	usePreload([
		[ factory.api, () => props.notifyNetworkMainMemberFactoryRequested() ],
		[ listing.api, () => props.notifyNetworkFamilyListingRequested() ]
	]);

	if (isRequestFreshOrPending(listing.api)) return null;

	return (
		<ResponsiveContainer classNames={style.container}>

			<Content>

				<Stepper
					steps={[
						{ current: false, completed: true, description: t('I tuoi dati'), route: getCardRequestFormRoute() },
						{ current: true, completed: false, description: t('I tuoi nuclei familiari') },
						{ current: false, completed: false, description: t('Riepilogo e pagamento') }
					]}
					onNext={() => navigate(getCheckoutRoute())}
				/>

				<p dangerouslySetInnerHTML={{
					__html: 'Gestisci i tuoi nuclei familiari. Parti dal tuo nucleo familiare principale, che corrisponde al tuo stato di famiglia. Puoi aggiungere anche nuclei familiari esterni. <span><strong>Questa operazione può essere completata anche in seguito all’attivazione della Carta Esercito.</strong></span>'
				}}/>

				{
					(
						_.some(listing.data.main, (x) => x.missingData)
						|| _.some(listing.data.externals, ({ members }) => _.some(members, (x) => x.missingData))
					) &&
					<MessageBox
						classnames={style.missingDataWarning}
						type={'warning'}
						message={t('Per procedere con la richiesta, compila tutti i dati relativi a ogni membro di ogni nucleo familiare.')}
					/>
				}

				<FamilyMembersContainer
					id={null}
					icon={Family}
					title={t('Il tuo nucleo familiare')}
					tag={t('Nucleo principale')}
					mainMember={profile!}
					members={listing.data.main}
				/>

				{
					listing.data.externals.map(({ id, members }, index) => (
						<FamilyMembersContainer
							key={id}
							id={id}
							icon={ExternalFamily}
							title={t(`Nucleo familiare esterno n°${index + 1}`)}
							tag={t('Nucleo esterno')}
							mainMember={profile!}
							members={members}
						/>
					))
				}

				<ActionButton
					classNames={style.addExternalFamily}
					text={t('Aggiungi nucleo familiare esterno')}
					isLoading={create.api.isLoading}
					onClick={() => props.notifyNetworkFamilyCreateRequested()}
				/>

			</Content>
		</ResponsiveContainer>
	);
};

interface FamilyMembersContainerProps {
	id: number | null;
	icon: string;
	title: string;
	tag: string;
	mainMember: IMainMember;
	members: IMember[];
}

const FamilyMembersContainer: FunctionComponent<FamilyMembersContainerProps> = (props) => {

	const [ memberFormStatus, setMemberFormStatus ] = useState<{ show: boolean; mode: 'CREATE' | 'EDIT'; member?: IMember; }>({
		show: false,
		mode: 'CREATE'
	});

	const { t } = useTranslation();

	return (
		<div className={style.familyMembersContainer}>

			<div className={style.header}>
				<ProImage src={props.icon}/>
				<p className={mergeClasses(style.membersCount, 'mobile-only')}>{`${1 + props.members.length} membri`}</p>
				<h4 className={style.title}>{props.title}</h4>
				<Tag classnames={style.membersCategory} title={props.tag}/>
				<p className={mergeClasses(style.membersCount, 'desktop-only')}>{`${1 + props.members.length} membri`}</p>
			</div>

			<div className={style.members}>

				<div className={style.member}>
					<ProImage className={style.profilePicture} src={'source'} hideWhenEmpty={false}/>
					<div className={style.detailsContainer}>
						<div className={style.detail}>
							<p className={style.title}>{t('Nome e Cognome')}</p>
							<p className={style.value}>{props.mainMember.name} {props.mainMember.surname}</p>
						</div>
						<div className={style.detail}>
							<Tag classnames={'m-0'} title={t('Caponucleo')}/>
						</div>
						<div className={style.detail}/>
						<div className={style.detail}/>
					</div>
				</div>

				{
					props.members.map((member, index) => {
						return (
							<FamilyMember
								key={index}
								member={member}
								onEdit={() => setMemberFormStatus({
									show: true, mode: 'EDIT', member
								})}
							/>
						);
					})
				}

			</div>

			<ActionButton
				classNames={style.addMember}
				text={t('Aggiungi membro al nucleo')}
				onClick={() => setMemberFormStatus({ show: true, mode: 'CREATE' })}
				icon={<ProImage className={'ml-half'} src={PlusCircle}/>}
			/>

			{
				memberFormStatus.show &&
				<FamilyMemberModalForm
					familyId={props.id}
					member={memberFormStatus.member}
					mode={memberFormStatus.mode}
					onDismiss={() => setMemberFormStatus({ ...memberFormStatus, show: false })}
				/>
			}

		</div>
	);
};

interface FamilyMemberProps {
	member: IMember;
	editable?: boolean;
	onEdit?: () => void;
}

const FamilyMember: FunctionComponent<FamilyMemberProps> = ({ member, ...props }) => {

	const { t } = useTranslation();

	const StatusTag = ({ classnames }: { classnames: string; }) => (
		<div className={mergeClasses(style.detail, classnames)}>
			{/*{*/}
			{/*	member.missingData &&*/}
			{/*	<div className={'w-100 h-100 display-flex flex-row justify-start align-items-center'}>*/}
			{/*		<Tag classnames={style.missingDataTag} title={t('Dati mancanti')}/>*/}
			{/*		<ProImage className={'ml-half'} src={Attention}/>*/}
			{/*	</div>*/}
			{/*}*/}
			{/*{*/}
			{/*	member.inApproval &&*/}
			{/*	<Tag classnames={style.inApprovalTag} title={t('Carta esercito in valutazione')}/>*/}
			{/*}*/}
		</div>
	);

	return (
		<div className={mergeClasses(style.member, { [style.missingData]: member.missingData })}>

			<ProImage className={style.profilePicture} src={member.profilePhoto}/>

			<div className={style.detailsContainer}>

				<StatusTag classnames={'mobile-only'}/>

				<div className={style.detail}>
					<p className={style.title}>{t('Nome e Cognome')}</p>
					<p className={style.value}>{member.name} {member.surname}</p>
				</div>

				{
					member.head &&
					<div className={style.detail}>
						<Tag classnames={'m-0'} title={t('Caponucleo')}/>
					</div>
				}

				{
					!member.head &&
					<div className={style.detail}>
						<p className={style.title}>{t('Legame di Parentela')}</p>
						<p className={style.value}>{_.find(RELATIONSHIP, x => x.key === member.relationship)?.value ?? '-'}</p>
					</div>
				}

				<StatusTag classnames={'desktop-only'}/>

				<div className={style.detail}>
					{
						(typeof props.editable == 'undefined' || props.editable) &&
						<div className={style.edit}>
							<div
								className={'display-flex flex-row justify-center align-items-center cursor-pointer'}
								onClick={UnwrapElementCallback(() => props.onEdit && props.onEdit())}
							>
								{t('Modifica')}
								<ProImage className={'ml-half'} src={Edit}/>
							</div>
						</div>
					}
				</div>

			</div>
		</div>
	);
};

export default FamilyMembers;
