import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import { IFamilyCreateRequest, IFamilyCreateResponse, INotifyNetworkFamilyCreateRequested, NETWORK_FAMILY_CREATE_REQUESTED } from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkFamilyCreateCompleted,
	notifyNetworkFamilyCreateError,
	notifyNetworkFamilyCreateFailed,
	notifyNetworkFamilyCreateInit
} from './actions';
import delay from '../../../../../network/effects/delay';
import { notifyNetworkFamilyListingRequested } from '../listing/actions';

const ENDPOINT = '/form/nucleo';

const api = ({ configuration }: IFamilyCreateRequest) => {
	return ConnectionCore.getInstance().post<IFamilyCreateResponse>(ENDPOINT);
};

function* entity(action: INotifyNetworkFamilyCreateRequested) {
	try {

		yield put(notifyNetworkFamilyCreateInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IFamilyCreateResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {
			yield all([
				put(notifyNetworkFamilyCreateCompleted({
					status: response.status,
					message: response.data.message
				})),
				put(notifyNetworkFamilyListingRequested())
			]);
		} else {
			yield put(notifyNetworkFamilyCreateFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkFamilyCreateError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_FAMILY_CREATE_REQUESTED, entity);
}

export default fork(watcher);
