import React, { FunctionComponent } from 'react';
import style from './style/PassportPhotoPicker.module.scss';
import ProImage from '../../../shared/ui/proImage';
import { ExternalLink } from '../../../../assets/images/externalLink';
import { mergeClasses, UnwrapElementCallback } from '../../../../helpers/utils';
import { Soldier } from '../../../../assets/images/soldier';
import { Upload } from '../../../../assets/images/upload';
import { useTranslation } from 'react-i18next';
import { Unit } from 'bytes';
import { TrashRed } from '../../../../assets/images/trash';
import ImagePicker from '../../../shared/utilities/imagePicker';
import { useNullableRef } from '../../../system/hooks';
import { IFormPicture } from '../../../../store/models/IFormPicture';

interface Props {
	photo: IFormPicture;
	maxSize?: number | `${number}${Unit}`;
	onPhotoPicked: (photo: IFormPicture) => void;
	onPhotoRejected?: () => void;
	onClear?: () => void;
}

const PassportPhotoPicker: FunctionComponent<Props> = (props) => {

	const pickerRef = useNullableRef<HTMLInputElement>();

	const { t } = useTranslation();

	return (
		<div className={'w-100'}>
			<header className={'w-100 display-flex flex-row justify-start align-items-center flex__wrap'}>
				<div className={'mr-1'}>
					<h5 className={'mr-half'} style={{ display: 'inline-block' }}>
						{t('Fototessera')}
					</h5>
					<span className={'ml-half'}>{t('(peso massimo: 8Mb; formato fototessera passaporto)')}</span>
				</div>
				<div className={'display-flex flex-row justify-center align-items-center'}>
					<a
						className={style.passportGuidelines}
						href={'#'}
					>
						{t('Consulta direttiva sulla fototessera')}
					</a>
					<ProImage className={'pl-1 cursor-pointer'} src={ExternalLink}/>
				</div>
			</header>
			{
				!props.photo.isAttached &&
				<div className={'w-100 display-flex flex-column justify-start align-items-start'}>
					<div
						className={style.documentContainer}
						onClick={UnwrapElementCallback(() => pickerRef.current?.click())}
					>
						<ProImage className={'mb-1'} src={Soldier}/>
						<div className={style.upload}>
							<p className={style.title}>{t('Carica fotografia')}</p>
							<ProImage src={Upload}/>
						</div>
						<ImagePicker
							ref={pickerRef}
							maxSize={props.maxSize ?? '8MB'}
							onImagePicked={(data) => {
								props.onPhotoPicked({
									data: data.image,
									name: data.file.name,
									mimeType: data.mimeType,
									extension: data.extension,
									isAttached: true
								});
							}}
							onImageRejected={() => props.onPhotoRejected && props.onPhotoRejected()}
						/>
					</div>
				</div>
			}
			{
				props.photo.isAttached &&
				<div className={'w-100 display-flex flex-column justify-start align-items-start'}>
					<div className={mergeClasses(style.documentContainer, style.picked)}>
						<ProImage className={'w-100 mb-1'} src={props.photo.data}/>
						<div
							className={style.deleteContainer}
							onClick={UnwrapElementCallback(() => props.onClear && props.onClear())}
						>
							<h6>{t('Elimina')}</h6>
							<ProImage src={TrashRed}/>
						</div>
					</div>
				</div>
			}
		</div>
	);
};

export default PassportPhotoPicker;
