export {};

Boolean.prototype.toNumber = function () {
	return +this;
};

Array.prototype.insert = function <T>(at: number, element: T) {
	this.splice(at, 0, element);
};

Array.prototype.move = function (from: number, to: number) {
	this.splice(to, 0, this.splice(from, 1)[0]);
};

String.prototype.withParams = function (...params: ConstructorParameters<typeof URLSearchParams>) {
	return `${this}?${(new URLSearchParams(...params)).toString()}`;
};
