import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { apiCompleted, apiError, apiFailed, apiInit, getApiDefaultState } from '../../../../../network/helpers';
import { IMainMemberPushFactoryState, MainMemberPushFactoryActions } from './types';
import {
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_CLEAR,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_COMPLETED,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_ERROR,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_FAILED,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT
} from '../../../network/mainMember/pushFactory/types';
import { ROUTER_HISTORY_CHANGED } from '../../../../../router/routerHistory/store/types';

const initialState = Object.freeze<IMainMemberPushFactoryState>({
	api: getApiDefaultState()
});

const MainMemberPushFactoryReducer: Reducer<IMainMemberPushFactoryState, MainMemberPushFactoryActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IMainMemberPushFactoryState>) => {
		switch (action.type) {
			case ROUTER_HISTORY_CHANGED: {
				return initialState;
			}
			case NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT: {
				apiInit(draft.api);
				break;
			}
			case NETWORK_MAIN_MEMBER_PUSH_FACTORY_COMPLETED: {

				const { status, message } = action.payload;

				apiCompleted(draft.api, status, message);
				break;
			}
			case NETWORK_MAIN_MEMBER_PUSH_FACTORY_FAILED: {

				const { status, message, validationErrors } = action.payload;

				apiFailed(draft.api, status, message, validationErrors);
				break;
			}
			case NETWORK_MAIN_MEMBER_PUSH_FACTORY_ERROR: {
				apiError(draft.api);
				break;
			}
			case NETWORK_MAIN_MEMBER_PUSH_FACTORY_CLEAR: {
				draft.api = initialState.api;
				break;
			}
		}
	});
};

export default MainMemberPushFactoryReducer;
