import React, { CSSProperties, FunctionComponent, useState } from 'react';
import style from './style/ProImage.module.scss';
import { mergeClasses } from '../../../../helpers/utils';

interface Props {
	className?: string;
	style?: CSSProperties;
	src?: string | null;
	srcFallback?: string | null;
	placeholder?: string;
	alt?: string;
	contain?: boolean;
	hideWhenEmpty?: boolean;
}

const TRANSPARENT_IMAGE = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNgYAAAAAMAASsJTYQAAAAASUVORK5CYII=';

const ProImage: FunctionComponent<Props> = (props) => {

	const [ hasErrors, setHasErrors ] = useState(false);

	// if (hasErrors || (props.hideWhenEmpty && isEmpty(props.src))) {
	// 	return null;
	// }

	const aspectFit = typeof props.contain == 'undefined' || props.contain;

	return (
		<img
			className={mergeClasses(
				style.proImage,
				{
					[style.aspectCover]: !aspectFit,
					[style.aspectFit]: aspectFit
				},
				props.className
			)}
			style={props.style}
			alt={props.alt}
			src={props.src || props.srcFallback || TRANSPARENT_IMAGE}
			draggable={false}
			onError={() => setHasErrors(true)}
		/>
	);
};

export default ProImage;
