import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IMainMemberFactoryPayload,
	IMainMemberFactoryRequest,
	INotifyNetworkMainMemberFactoryClear,
	INotifyNetworkMainMemberFactoryCompleted,
	INotifyNetworkMainMemberFactoryError,
	INotifyNetworkMainMemberFactoryFailed,
	INotifyNetworkMainMemberFactoryInit,
	INotifyNetworkMainMemberFactoryRequested,
	NETWORK_MAIN_MEMBER_FACTORY_CLEAR,
	NETWORK_MAIN_MEMBER_FACTORY_COMPLETED,
	NETWORK_MAIN_MEMBER_FACTORY_ERROR,
	NETWORK_MAIN_MEMBER_FACTORY_FAILED,
	NETWORK_MAIN_MEMBER_FACTORY_INIT,
	NETWORK_MAIN_MEMBER_FACTORY_REQUESTED
} from './types';

export const notifyNetworkMainMemberFactoryRequested = (data: IMainMemberFactoryRequest = {}): INotifyNetworkMainMemberFactoryRequested => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_FACTORY_REQUESTED, data);
};

export const notifyNetworkMainMemberFactoryInit = (): INotifyNetworkMainMemberFactoryInit => {
	return notifyAction(NETWORK_MAIN_MEMBER_FACTORY_INIT);
};

export const notifyNetworkMainMemberFactoryCompleted = (data: IMainMemberFactoryPayload): INotifyNetworkMainMemberFactoryCompleted => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_FACTORY_COMPLETED, data);
};

export const notifyNetworkMainMemberFactoryFailed = (data: Unsuccessful<IMainMemberFactoryPayload>): INotifyNetworkMainMemberFactoryFailed => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_FACTORY_FAILED, data);
};

export const notifyNetworkMainMemberFactoryError = (): INotifyNetworkMainMemberFactoryError => {
	return notifyAction(NETWORK_MAIN_MEMBER_FACTORY_ERROR);
};

export const notifyNetworkMainMemberFactoryClear = (): INotifyNetworkMainMemberFactoryClear => {
	return notifyAction(NETWORK_MAIN_MEMBER_FACTORY_CLEAR);
};
