import Checkout from './Checkout';
import { connect } from 'react-redux';
import { ICheckoutActionsProps, ICheckoutStateProps } from './store/props';
import { RootState } from '../../../../store/rootReducer';
import { notifyNetworkFamilyListingClear, notifyNetworkFamilyListingRequested } from '../../network/family/listing/actions';
import { notifyNetworkMainMemberFactoryClear, notifyNetworkMainMemberFactoryRequested } from '../../network/mainMember/factory/actions';

const mapStateToProps = (state: RootState): ICheckoutStateProps => {
	return {
		factory: state.cardRequestModule.mainMember.factory,
		listing: state.cardRequestModule.family.listing,
		profile: state.cardRequestModule.mainMember.profile
	};
};

const mapDispatchToProps: ICheckoutActionsProps = {
	notifyNetworkMainMemberFactoryRequested,
	notifyNetworkMainMemberFactoryClear,
	notifyNetworkFamilyListingRequested,
	notifyNetworkFamilyListingClear
};

export default connect(mapStateToProps, mapDispatchToProps)(Checkout);
