import React, { FunctionComponent } from 'react';
import ReactCollapsible from 'react-collapsible';

interface Props {
	identifier?: string;
	isOpen: boolean;
	onOpen?: (identifier?: string) => void;
	onClose?: (identifier?: string) => void;
}

const Collapsible: FunctionComponent<Props> = (props) => {
	return (
		<ReactCollapsible
			className={'w-100'}
			openedClassName={'w-100'}
			open={props.isOpen}
			lazyRender={false}
			contentHiddenWhenClosed={false}
			transitionTime={220}
			transitionCloseTime={220}
			onOpen={() => props.onOpen && props.onOpen(props.identifier)}
			onClose={() => props.onClose && props.onClose(props.identifier)}
			trigger={''}
		>
			<div className={'w-100'}>
				{props.children}
			</div>
		</ReactCollapsible>
	);
};

export default Collapsible;
