import React, { useCallback } from 'react';
import { Location as HistoryLocation, Navigate, Route, Routes } from 'react-router-dom';
import RouterHistory from './routerHistory';
import browserHistory from './browserHistory';
import CustomBrowserRouter from './customBrowserRouter';
import { scrollToTop } from '../helpers/utils';
import { HistoryAction } from '../types/historyAction';
import NotFound from '../pages/notFound';
import CardRequestModuleRouter from '../modules/cardRequest/CardRequestModuleRouter';

const RootRoutes = () => (
	<Routes>
		{CardRequestModuleRouter}
		<Route path={'/404'} element={<NotFound/>}/>
		<Route path={'*'} element={<Navigate to={'/404'}/>}/>
	</Routes>
);

const Router = () => {

	const onHistoryChange = useCallback((action: HistoryAction, location: HistoryLocation) => {

		if (action === 'PUSH') {
			// Scrolling new page to top
			scrollToTop();
		}

	}, []);

	return (
		<CustomBrowserRouter history={browserHistory.getHistory()}>
			<RouterHistory onHistoryChange={onHistoryChange}/>
			<RootRoutes/>
		</CustomBrowserRouter>
	);
};

export default Router;
