import React, { FunctionComponent } from 'react';
import style from './style/MessageBox.module.scss';
import ProImage from '../../../shared/ui/proImage';
import { mergeClasses } from '../../../../helpers/utils';
import { Info } from '../../../../assets/images/info';
import { CircleCheck } from '../../../../assets/images/check/check';
import { Warning } from '../../../../assets/images/warning';

interface Props {
	classnames?: string;
	message: string;
	imageUrl?: string;
	type?: 'info' | 'success' | 'warning';
}

const MessageBox: FunctionComponent<Props> = (props) => {

	let type = props.type ?? 'success';

	let imageUrl = props.imageUrl;
	if (typeof imageUrl == 'undefined') {
		if (type === 'info') {
			imageUrl = Info;
		} else if (type === 'success') {
			imageUrl = CircleCheck;
		} else if (type === 'warning') {
			imageUrl = Warning;
		}
	}

	return (
		<div
			className={mergeClasses(
				style.container,
				style.success,
				{
					[style.info]: type === 'info',
					[style.warning]: type === 'warning'
				},
				props.classnames
			)}
		>
			{
				imageUrl &&
				<div className={style.icon}>
					<ProImage src={imageUrl}/>
				</div>
			}
			<p className={mergeClasses(style.title, { ['m-0']: !imageUrl })}>{props.message}</p>
		</div>
	);
};

export default MessageBox;
