import CardRequestForm from './CardRequestForm';
import { connect } from 'react-redux';
import { IMainMemberFormActionsProps, IMainMemberFormStateProps } from './store/props';
import { RootState } from '../../../../store/rootReducer';
import { notifyNetworkMainMemberFactoryClear, notifyNetworkMainMemberFactoryRequested } from '../../network/mainMember/factory/actions';
import { notifyNetworkMainMemberPushFactoryClear, notifyNetworkMainMemberPushFactoryRequested } from '../../network/mainMember/pushFactory/actions';
import { notifyMainMemberFactoryClearForm, notifyMainMemberFactoryUpdateForm } from '../../store/mainMember/factory/actions';

const mapStateToProps = (state: RootState): IMainMemberFormStateProps => {
	return {
		factory: state.cardRequestModule.mainMember.factory,
		update: state.cardRequestModule.mainMember.update
	};
};

const mapDispatchToProps: IMainMemberFormActionsProps = {
	notifyNetworkMainMemberFactoryRequested,
	notifyNetworkMainMemberFactoryClear,
	notifyNetworkMainMemberPushFactoryRequested,
	notifyNetworkMainMemberPushFactoryClear,
	notifyMainMemberFactoryUpdateForm,
	notifyMainMemberFactoryClearForm
};

export default connect(mapStateToProps, mapDispatchToProps)(CardRequestForm);
