import React, { FunctionComponent, useMemo, useState } from 'react';
import Select from 'react-select';
import { StylesConfig } from 'react-select/dist/declarations/src/styles';
import _ from 'lodash';
import { IValidationErrors } from '../../../../../network/models/IValidationErrors';
import { isEmpty } from '../../../../../helpers/utils';
import { SelectOption } from '../../../../../types/selectOption';

interface Props {
	name: string;
	value: string;
	defaultValue: string;
	options: SelectOption[];
	placeholder?: string;
	label?: string;
	required?: boolean;
	disabled?: boolean;
	searchable?: boolean;
	clearable?: boolean;
	validationErrors?: IValidationErrors;
	onSelect: (payload?: SelectOption) => void;
	styles?: StylesConfig;
}

const InputSelect: FunctionComponent<Props> = (props) => {

	const styles = useMemo<StylesConfig>(() => {
		return {
			container: (provided, state) => ({
				...provided,
				width: '100%',
				display: 'flex',
				flexDirection: 'row',
				alignItems: 'center',
				position: 'relative',
				boxShadow: state.isFocused ? '0 1px 3px 0 #cfd7df !important' : 'none'
			}),
			control: (styles) => ({
				...styles,
				backgroundColor: props.disabled ? '#F4F4F4' : 'white',
				borderRadius: '0',
				border: '0',
				outline: 'none',
				boxShadow: 'none',
				width: '100%',
				height: '2.8rem',
				borderBottom: '.15rem solid #7D7D7D !important'
			}),
			input: (provided, state) => ({
				...provided,
				width: '100%',
				margin: 'inherit',
				padding: '0 !important',
				visibility: 'inherit',
				color: '#525252',
				boxSizing: 'inherit',
				borderBottom: 'inherit',
				alignItems: 'stretch',
				textTransform: 'uppercase',
				boxShadow: 'none',
				outline: 'none',
				border: 'none',
				fontSize: '.85rem',
				lineHeight: '1rem',
				fontWeight: '600',
				'& > input': {
					padding: '1rem 0 0 0 !important',
					boxShadow: 'none !important'
				}
			}),
			placeholder: (provided, state) => ({
				...provided,
				margin: '0 !important',
				padding: '1rem 0 0 0 !important',
				fontSize: '.8rem',
				maxLines: '1',
				overflow: 'hidden',
				whiteSpace: 'nowrap',
				textOverflow: 'ellipsis',
				display: 'block',
				userSelect: 'none'
			}),
			singleValue: (provided, state) => ({
				...provided,
				display: 'flex',
				width: '100%',
				paddingTop: '1rem',
				margin: '0 !important',
				color: '#525252',
				fontSize: '.85rem',
				fontWeight: '600',
				textTransform: 'none',
				boxShadow: 'none'
			}),
			valueContainer: (provided, state) => ({
				...provided,
				textTransform: 'none',
				width: '100%',
				padding: '0 .625rem'
			}),
			indicatorsContainer: (provided, state) => ({
				...provided,
				paddingBottom: '0'
			}),
			menu: (provided, state) => ({
				...provided,
				borderRadius: '.5rem',
				textTransform: 'none',
				width: '100%',
				padding: '0',
				color: '#7D7D7D'
			}),
			option: (provided, state) => ({
				...provided,
				color: state.isSelected || state.isFocused ? 'white' : 'inherit',
				backgroundColor: state.isSelected ? '#7D7D7D' : (state.isFocused ? '#b2b2b2' : 'inherit'),
				cursor: 'pointer'
			}),
			...(props.styles ?? {})
		};
	}, [ props.styles, props.disabled ]);

	const [ isCleared, setIsCleared ] = useState(false);

	const value = useMemo<SelectOption | undefined>(() => {
		if (isEmpty(props.value) && isEmpty(props.defaultValue)) return undefined;
		return _.find(props.options, [ 'value', props.value ?? props.defaultValue ]);
	}, [ props.options, props.value, props.defaultValue ]);

	return (
		<div className={'position__relative'}>
			<label className={'labelInputSelect'}>
				{props.label} {props.required ? '*' : ''}
			</label>
			<Select
				key={value?.value ?? ''}
				value={!isCleared ? value : ''}
				isDisabled={!!props.disabled}
				isSearchable={typeof props.searchable == 'undefined' || props.searchable}
				isClearable={typeof props.clearable == 'undefined' || props.clearable}
				isLoading={false}
				isRtl={false}
				isMulti={false}
				options={props.options}
				onChange={(data) => {
					setIsCleared(!data);
					props.onSelect(data as SelectOption);
				}}
				placeholder={props.placeholder ?? ''}
				styles={styles}
			/>
			<input
				name={props.name}
				type={'hidden'}
				value={value ? JSON.stringify(value) : ''}
			/>
			{/*{*/}
			{/*	!isEmpty(validationError) &&*/}
			{/*	<div className={'input-validation-error animation__bounce_in'}>*/}
			{/*		<span>{validationError}</span>*/}
			{/*	</div>*/}
			{/*}*/}
		</div>
	);
};

export default InputSelect;
