import React from 'react';
import ErrorBoundary from '../../utilities/errorBoundary';
import { useStrictNavigate } from '../../hooks';
import { useLocation } from 'react-router-dom';

const withErrorBoundary = <T extends object>(Component: React.FunctionComponent<T>) => (props: T) => {

	const location = useLocation();
	const navigate = useStrictNavigate();

	return (
		<ErrorBoundary location={location} navigate={navigate}>
			<Component {...props}/>
		</ErrorBoundary>
	);
};

export default withErrorBoundary;
