import React, { ChangeEventHandler, CSSProperties, FunctionComponent } from 'react';
import { useTranslation } from 'react-i18next';
import { IValidationErrors } from '../../../../../network/models/IValidationErrors';
import { mergeClasses } from '../../../../../helpers/utils';

interface Props {
	type?: 'text' | 'email' | 'search' | 'url' | 'tel' | 'date';
	className?: string;
	style?: CSSProperties;
	label?: string;
	maxLength?: number;
	name: string;
	value?: string;
	onChange?: ChangeEventHandler<HTMLInputElement>;
	placeholder?: string;
	required?: boolean;
	readonly?: boolean;
	disabled?: boolean;
	validationErrors?: IValidationErrors;
	onClear?: () => void;
	min?: string;
	max?: string;
	validate?: (value?: string) => boolean;
}

const DEFAULT_MAX_LENGTH = 191;

const Input: FunctionComponent<Props> = (props) => {

	const { t } = useTranslation();

	const maxLengthProps = props.maxLength ?? DEFAULT_MAX_LENGTH;

	return (
		<div className={'form-element'}>
			<input
				className={mergeClasses(
					props.className,
					{ ['invalid']: props.validate && !props.validate(props.value) }
				)}
				style={props.style}
				maxLength={props.maxLength ?? DEFAULT_MAX_LENGTH}
				name={props.name}
				onChange={(e) => props.onChange && props.onChange(e)}
				disabled={props.disabled}
				placeholder={props.placeholder}
				readOnly={props.readonly ?? false}
				required={props.required}
				type={props.type ?? 'text'}
				value={props.value ?? ''}
				min={props.min}
				max={props.max}
			/>
			<label className={'labelInput'}>{props.label} {props.required ? '*' : ''}</label>
			{
				props.value?.length === maxLengthProps &&
				<div className={'input-validation-notice animation__bounce_in'}>
					<span>{t('forms.input.maxlength.notice')}</span>
				</div>
			}
			{/*{*/}
			{/*	!isEmpty(validationError) &&*/}
			{/*	<div className={'input-validation-error animation__bounce_in'}>*/}
			{/*		<span>{validationError}</span>*/}
			{/*	</div>*/}
			{/*}*/}
		</div>
	);
};

export default Input;
