import React, { ChangeEventHandler, CSSProperties, FunctionComponent } from 'react';
import { useTranslation } from 'react-i18next';
import { IValidationErrors } from '../../../../../network/models/IValidationErrors';
import { isEmpty } from '../../../../../helpers/utils';

interface Props {
	className?: string;
	style?: CSSProperties;
	label?: string;
	maxLength?: number;
	name: string;
	value?: string;
	onChange?: ChangeEventHandler<HTMLTextAreaElement>;
	placeholder?: string;
	required?: boolean;
	readonly?: boolean;
	disabled?: boolean;
	validationErrors?: IValidationErrors;
	onClear?: () => void;
}

const DEFAULT_MAX_LENGTH = 2000;

const Textarea: FunctionComponent<Props> = (props) => {

	const { t } = useTranslation();

	const maxLengthProps = props.maxLength ?? DEFAULT_MAX_LENGTH;

	return (
		<div className={'w-100 form-element'}>
			{
				!isEmpty(props.label) &&
				<label className={'labelTextarea'}>{props.label} {props.required ? '*' : ''}</label>
			}
			<textarea
				className={props.className}
				style={props.style}
				maxLength={props.maxLength ?? DEFAULT_MAX_LENGTH}
				name={props.name}
				onChange={(e) => props.onChange && props.onChange(e)}
				disabled={props.disabled}
				placeholder={props.placeholder}
				readOnly={props.readonly ?? false}
				required={props.required}
				value={props.value ?? ''}
			/>
			{
				props.value?.length === maxLengthProps &&
				<div className={'textarea-validation-notice animation__bounce_in'} style={{ backgroundColor: 'white' }}>
					<span>{t('forms.input.maxlength.notice')}</span>
				</div>
			}
			{/*{*/}
			{/*	!isEmpty(validationError) &&*/}
			{/*	<div className={'input-validation-error animation__bounce_in'}>*/}
			{/*		<span>{validationError}</span>*/}
			{/*	</div>*/}
			{/*}*/}
		</div>
	);
};

export default Textarea;
