import {
	STATUS_CODE_BAD_REQUEST,
	STATUS_CODE_DEFAULT,
	STATUS_CODE_FORBIDDEN,
	STATUS_CODE_INTERNAL_SERVER_ERROR,
	STATUS_CODE_NOT_FOUND,
	STATUS_CODE_REQUEST_TIMEOUT,
	STATUS_CODE_SUCCESS,
	STATUS_CODE_SUCCESS_NO_CONTENT,
	STATUS_CODE_UNAUTHORIZED,
	STATUS_CODE_UNPROCESSABLE_ENTITY
} from './httpStatus';
import { IApiState } from './models/IApiState';

export const setTokenScheme = (token: string): string => {
	return `Bearer ${token}`;
};

export const isDefaultStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_DEFAULT;
};

export const isSuccessStatusCode = (status: number, strict: boolean = true): boolean => {
	if (strict) {
		return status === STATUS_CODE_SUCCESS;
	}
	return status >= STATUS_CODE_SUCCESS && status < 300;
};

export const isNoContentStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_SUCCESS_NO_CONTENT;
};

export const isBadRequestStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_BAD_REQUEST;
};

export const isUnauthorizedStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_UNAUTHORIZED;
};

export const isForbiddenStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_FORBIDDEN;
};

export const isNotFoundStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_NOT_FOUND;
};

export const isTimeoutStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_REQUEST_TIMEOUT;
};

export const isUnprocessableEntityStatusCode = (status: number): boolean => {
	return status === STATUS_CODE_UNPROCESSABLE_ENTITY;
};

export const isInternalServerError = (status: number): boolean => {
	return status === STATUS_CODE_INTERNAL_SERVER_ERROR;
};

export const isRequestSuccessful = <T extends IApiState['api']>(request: T): boolean => {
	return !isDefaultStatusCode(request.status) && isSuccessStatusCode(request.status, false);
};

export const isRequestUnsuccessful = <T extends IApiState['api']>(request: T): boolean => {
	return !isDefaultStatusCode(request.status) && !isSuccessStatusCode(request.status, false);
};

export const isRequestFreshOrPending = <T extends IApiState['api']>(request: T): boolean => {
	if (!isDefaultStatusCode(request.status) && !request.isFailed && !request.isError) return false;
	return request.isLoading || isDefaultStatusCode(request.status);
};
