import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IMainMemberPushFactoryPayload,
	IMainMemberPushFactoryRequest,
	INotifyNetworkMainMemberPushFactoryClear,
	INotifyNetworkMainMemberPushFactoryCompleted,
	INotifyNetworkMainMemberPushFactoryError,
	INotifyNetworkMainMemberPushFactoryFailed,
	INotifyNetworkMainMemberPushFactoryInit,
	INotifyNetworkMainMemberPushFactoryRequested,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_CLEAR,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_COMPLETED,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_ERROR,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_FAILED,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT,
	NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED
} from './types';

export const notifyNetworkMainMemberPushFactoryRequested = (data: IMainMemberPushFactoryRequest): INotifyNetworkMainMemberPushFactoryRequested => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED, data);
};

export const notifyNetworkMainMemberPushFactoryInit = (): INotifyNetworkMainMemberPushFactoryInit => {
	return notifyAction(NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT);
};

export const notifyNetworkMainMemberPushFactoryCompleted = (data: IMainMemberPushFactoryPayload): INotifyNetworkMainMemberPushFactoryCompleted => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_PUSH_FACTORY_COMPLETED, data);
};

export const notifyNetworkMainMemberPushFactoryFailed = (data: Unsuccessful<IMainMemberPushFactoryPayload>): INotifyNetworkMainMemberPushFactoryFailed => {
	return notifyActionWithPayload(NETWORK_MAIN_MEMBER_PUSH_FACTORY_FAILED, data);
};

export const notifyNetworkMainMemberPushFactoryError = (): INotifyNetworkMainMemberPushFactoryError => {
	return notifyAction(NETWORK_MAIN_MEMBER_PUSH_FACTORY_ERROR);
};

export const notifyNetworkMainMemberPushFactoryClear = (): INotifyNetworkMainMemberPushFactoryClear => {
	return notifyAction(NETWORK_MAIN_MEMBER_PUSH_FACTORY_CLEAR);
};
