import React, { FunctionComponent } from 'react';
import style from './style/DocumentPicker.module.scss';
import { mergeClasses, UnwrapElementCallback } from '../../../../helpers/utils';
import ProImage from '../../../shared/ui/proImage';
import { FilePdf, FilePdfDark } from '../../../../assets/images/filePdf/filePdf';
import { useTranslation } from 'react-i18next';
import FilePicker from '../../../shared/utilities/filePicker';
import { useNullableRef } from '../../../system/hooks';
import { Unit } from 'bytes';
import { TrashRed } from '../../../../assets/images/trash';
import { IFormFile } from '../../../../store/models/IFormFile';
import { blobToBase64String } from 'blob-util';

interface Props {
	title: string;
	subtitle?: string;
	document: IFormFile;
	maxSize?: number | `${number}${Unit}`;
	onFilePicked: (document: IFormFile) => void;
	onFileRejected?: () => void;
	onClear?: () => void;
}

const DocumentPicker: FunctionComponent<Props> = (props) => {

	const pickerRef = useNullableRef<HTMLInputElement>();

	const { t } = useTranslation();

	return (
		<div className={'w-100'}>
			<header className={style.header}>
				<h5 className={'mr-half'} style={{ display: 'inline-block' }}>
					{props.title}
				</h5>
				{
					props.subtitle &&
					<span className={'ml-half'}>{props.subtitle}</span>
				}
			</header>
			{
				!props.document.isAttached &&
				<div
					className={style.container}
					onClick={UnwrapElementCallback(() => pickerRef.current?.click())}
				>
					<h6 className={mergeClasses(style.title, style.light)}>{t('Scegli un file da allegare')}</h6>
					<ProImage className={'ml-1'} src={FilePdf}/>
					<FilePicker
						ref={pickerRef}
						mimeTypes={'application/pdf'}
						maxSize={props.maxSize ?? '8MB'}
						onFilePicked={async (file, mimeType, extension) => {
							try {
								props.onFilePicked({
									data: await blobToBase64String(file),
									name: file.name,
									mimeType,
									extension,
									isAttached: true
								});
							} catch (e) {
								console.error(e);
							}
						}}
						onFileRejected={props.onFileRejected}
					/>
				</div>
			}
			{
				props.document.isAttached &&
				<div className={mergeClasses(style.container, style.picked)}>
					<div className={style.documentContainer}>
						<ProImage className={'mr-1'} src={FilePdfDark}/>
						<h6 className={mergeClasses(style.title, style.dark)}>{props.document.name}</h6>
					</div>
					<div
						className={style.deleteContainer}
						onClick={UnwrapElementCallback(() => props.onClear && props.onClear())}
					>
						<h6>{t('Elimina')}</h6>
						<ProImage src={TrashRed}/>
					</div>
				</div>
			}
		</div>
	);
};

export default DocumentPicker;
