import createSagaMiddleware, { END, SagaMiddleware, Task } from 'redux-saga';
import RootSaga from './rootSaga';

export default class SagaManager {

	private task: Task | undefined;
	private readonly saga: SagaMiddleware<object>;

	constructor() {
		this.saga = createSagaMiddleware();
	}

	run = () => this.task = this.saga.run(RootSaga);

	getMiddleware = () => this.saga;

	getTask = () => this.task;

	getEndAction = () => END;
}
