import React, { FunctionComponent } from 'react';
import style from './style/Content.module.scss';
import { mergeClasses } from '../../../../helpers/utils';

interface Props {
	classNames?: string;
}

const Content: FunctionComponent<Props> = (props) => {
	return (
		<main className={mergeClasses(style.container, props.classNames)}>
			{props.children}
		</main>
	);
};

export default Content;
