import React, { FunctionComponent } from 'react';
import { isEmpty, UnwrapElementCallback } from '../../../../../helpers/utils';

type RadioOption = { label: string; value: string; };

interface Props {
	name: string;
	value?: string;
	onChange: (option: RadioOption) => void;
	options: RadioOption[];
	optionClassnames?: string;
	labelClassnames?: string;
}

const InputRadio: FunctionComponent<Props> = (props) => {

	let defaultValue = props.value;
	if (isEmpty(defaultValue) && props.options.length > 0) {
		defaultValue = props.options[0].value;
	}

	const radioButtons = props.options?.map((option) => (
		<div key={option.value} className={props.optionClassnames}>
			<input
				type={'radio'}
				id={option.value}
				name={props.name}
				checked={option.value === defaultValue}
				onChange={UnwrapElementCallback(() => props.onChange && props.onChange(option))}
			/>
			<label
				className={props.labelClassnames}
				htmlFor={option.value}
			>
				{option.label}
			</label>
		</div>
	));

	return (
		<>
			{
				props.options.length === 1
					? radioButtons
					: (
						<fieldset className={'w-100'}>
							{radioButtons}
						</fieldset>
					)
			}
		</>
	);
};

export default InputRadio;
