import React, { FunctionComponent } from 'react';
import { Helmet } from 'react-helmet-async';
import { sprintf } from 'sprintf-js';
import { DEFAULT_HEAD_TITLE, DEFAULT_HEAD_TITLE_TEMPLATE } from '../../../../constants';

interface Props {
	title?: string;
}

const HeadComposer: FunctionComponent<Props> = ({ title, children, ...props }) => {

	title = sprintf(DEFAULT_HEAD_TITLE_TEMPLATE, title) || DEFAULT_HEAD_TITLE;

	return (
		<Helmet title={title}>
			{children}
		</Helmet>
	);
};

export default HeadComposer;
