import React from 'react';
import { Route } from 'react-router-dom';
import Homepage from './pages/homepage';
import CardRequestForm from './pages/cardRequestForm';
import FamilyMembers from './pages/familyMembers';
import Checkout from './pages/checkout';
import OrderConfirmation from './pages/orderConfirmation';

/*
|--------------------------------------------------------------------------
| Patterns
|--------------------------------------------------------------------------
*/
export const CardRequestModuleRouterPatterns = Object.freeze({
	'Homepage': '/',
	'CardRequestForm': '/richiesta',
	'Checkout': '/checkout',
	'FamilyMembers': '/nucleo',
	'OrderConfirmation': '/conferma-ordine'
});

/*
|--------------------------------------------------------------------------
| Router
|--------------------------------------------------------------------------
*/
const CardRequestModuleRouter = (
	<>
		<Route path={CardRequestModuleRouterPatterns['Homepage']} element={<Homepage/>}/>
		<Route path={CardRequestModuleRouterPatterns['CardRequestForm']} element={<CardRequestForm/>}/>
		<Route path={CardRequestModuleRouterPatterns['FamilyMembers']} element={<FamilyMembers/>}/>
		<Route path={CardRequestModuleRouterPatterns['Checkout']} element={<Checkout/>}/>
		<Route path={CardRequestModuleRouterPatterns['OrderConfirmation']} element={<OrderConfirmation/>}/>
	</>
);

export default CardRequestModuleRouter;
