import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import { IMemberCreateRequest, IMemberCreateResponse, INotifyNetworkMemberCreateRequested, NETWORK_MEMBER_CREATE_REQUESTED } from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkMemberCreateCompleted,
	notifyNetworkMemberCreateError,
	notifyNetworkMemberCreateFailed,
	notifyNetworkMemberCreateInit
} from './actions';
import delay from '../../../../../network/effects/delay';
import { notifyNetworkFamilyListingRequested } from '../../family/listing/actions';

const ENDPOINT = '/form/nucleo/membro';

const api = ({ data, configuration }: IMemberCreateRequest) => {
	return ConnectionCore.getInstance().post<IMemberCreateResponse>(ENDPOINT, data, {
		headers: configuration?.headers
	});
};

function* entity(action: INotifyNetworkMemberCreateRequested) {
	try {

		yield put(notifyNetworkMemberCreateInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IMemberCreateResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {
			yield all([
				put(notifyNetworkMemberCreateCompleted({
					status: response.status,
					message: response.data.message
				})),
				put(notifyNetworkFamilyListingRequested())
			]);
		} else {
			yield put(notifyNetworkMemberCreateFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkMemberCreateError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_MEMBER_CREATE_REQUESTED, entity);
}

export default fork(watcher);
