import Axios, { AxiosInstance, AxiosRequestConfig } from 'axios';
import DefaultHeaders from './defaultHeaders';
import { isEmpty } from '../helpers/utils';
import { DEVELOPMENT_MODE } from '../constants';
import { getLocalization } from '../modules/system/providers/localization/localization';
import { setTokenScheme } from './utils';

export interface ConnectionCoreConfig {
	baseUrl: string;
	getToken?: () => string;
	onResponse?: (data: { url: string, status: number }) => void;
}

export class ConnectionCore {

	private static config: ConnectionCoreConfig;
	private static instances: { [key: string]: AxiosInstance };

	static init = (config: ConnectionCoreConfig) => {
		ConnectionCore.config = config;
		ConnectionCore.instances = {};
		ConnectionCore.createInstance(config.baseUrl);
		ConnectionCore.setInterceptors(config.baseUrl);
	};

	static getInstance = (baseUrl?: string, config?: AxiosRequestConfig): AxiosInstance => {
		baseUrl = baseUrl ?? ConnectionCore.config.baseUrl;
		const instance = ConnectionCore.instances[baseUrl];
		if (instance) return instance;
		return (ConnectionCore.instances[baseUrl] = ConnectionCore.createInstance(baseUrl, config));
	};

	static setDefaultHeader = (key: string, value: string, baseUrl?: string) => {
		ConnectionCore.getInstance(baseUrl).defaults.headers.common[key] = value;
	};

	static removeDefaultHeader = (key: string, baseUrl?: string) => {
		delete ConnectionCore.getInstance(baseUrl).defaults.headers.common[key];
	};

	private static createInstance = (baseUrl: string, config?: AxiosRequestConfig) => {
		if (!config) {
			config = {
				baseURL: baseUrl,
				timeout: DEVELOPMENT_MODE ? 1000 * 5 : 1000 * 10,
				validateStatus: undefined,
				maxRedirects: 0,
				headers: {
					[DefaultHeaders.ACCEPT]: 'application/json',
					[DefaultHeaders.CONTENT_TYPE]: 'application/json'
				},
				transitional: {
					silentJSONParsing: true,
					forcedJSONParsing: true,
					clarifyTimeoutError: false
				}
			};
		}
		const instance = Axios.create(config);
		if (DEVELOPMENT_MODE) {
			instance.interceptors.request.use((config) => {
				console.log(`Request to: ${config.url}`);
				return config;
			});
		}
		return instance;
	};

	private static setInterceptors = (baseUrl?: string) => {

		ConnectionCore.getInstance(baseUrl).interceptors.request.use((config) => {

			if (!config.headers) return config;

			// Language
			const { code } = getLocalization();
			if (!isEmpty(code)) {
				config.headers[DefaultHeaders.CONTENT_LANGUAGE] = code;
			}

			// Token
			if (ConnectionCore.config.getToken) {
				const token = ConnectionCore.config.getToken();
				if (!isEmpty(token) && isEmpty(config.headers[DefaultHeaders.AUTHORIZATION] as string)) {
					config.headers[DefaultHeaders.AUTHORIZATION] = setTokenScheme(token);
				}
			}

			return config;
		});

		ConnectionCore.getInstance(baseUrl).interceptors.response.use((response) => {

			const { status, config: { url } } = response;

			if (ConnectionCore.config.onResponse) {
				ConnectionCore.config.onResponse({
					url: baseUrl && url ? `${baseUrl}${url}` : '',
					status
				});
			}
			return response;
		}, Promise.reject);

	};

}
