import { IApiRequest } from '../../../../../network/models/IApiRequest';
import { StoreAction } from '../../../../../store/models/actions/storeAction';
import { BasicStoreAction } from '../../../../../store/models/actions/basicStoreAction';
import { IApiPayload, Unsuccessful } from '../../../../../network/models/IApiPayload';
import { IApiResponse } from '../../../../../network/models/IApiResponse';
import { IFamily } from '../../../models/IFamily';
import { IMember } from '../../../models/IMember';

export type IFamilyListingRequest = IApiRequest

export type IFamilyListingResponse = IApiResponse<{ main: IMember[]; externals: IFamily[]; }>

export type IFamilyListingPayload = IApiPayload<IFamilyListingResponse['data']>

export const NETWORK_FAMILY_LISTING_REQUESTED = 'NETWORK_FAMILY_LISTING_REQUESTED';
export const NETWORK_FAMILY_LISTING_INIT = 'NETWORK_FAMILY_LISTING_INIT';
export const NETWORK_FAMILY_LISTING_COMPLETED = 'NETWORK_FAMILY_LISTING_COMPLETED';
export const NETWORK_FAMILY_LISTING_FAILED = 'NETWORK_FAMILY_LISTING_FAILED';
export const NETWORK_FAMILY_LISTING_ERROR = 'NETWORK_FAMILY_LISTING_ERROR';
export const NETWORK_FAMILY_LISTING_CLEAR = 'NETWORK_FAMILY_LISTING_CLEAR';

export type INotifyNetworkFamilyListingRequested = StoreAction<typeof NETWORK_FAMILY_LISTING_REQUESTED, IFamilyListingRequest>
export type INotifyNetworkFamilyListingInit = BasicStoreAction<typeof NETWORK_FAMILY_LISTING_INIT>
export type INotifyNetworkFamilyListingCompleted = StoreAction<typeof NETWORK_FAMILY_LISTING_COMPLETED, IFamilyListingPayload>
export type INotifyNetworkFamilyListingFailed = StoreAction<typeof NETWORK_FAMILY_LISTING_FAILED, Unsuccessful<IFamilyListingPayload>>
export type INotifyNetworkFamilyListingError = BasicStoreAction<typeof NETWORK_FAMILY_LISTING_ERROR>
export type INotifyNetworkFamilyListingClear = BasicStoreAction<typeof NETWORK_FAMILY_LISTING_CLEAR>

export type FamilyListingNetworkActions =
	INotifyNetworkFamilyListingRequested
	| INotifyNetworkFamilyListingInit
	| INotifyNetworkFamilyListingCompleted
	| INotifyNetworkFamilyListingFailed
	| INotifyNetworkFamilyListingError
	| INotifyNetworkFamilyListingClear
	;
