import React, { FunctionComponent } from 'react';
import style from './style/Stepper.module.scss';
import { isEmpty, mergeClasses, UnwrapElementCallback } from '../../../../helpers/utils';
import ProImage from '../../../shared/ui/proImage';
import { Check } from '../../../../assets/images/check/check';
import ActionButton from '../actionButton';
import { useTranslation } from 'react-i18next';
import { useStrictNavigate } from '../../../system/hooks';
import _ from 'lodash';

type StepType = {
	completed: boolean;
	current: boolean;
	description: string;
	route?: string;
};

interface StepperProps {
	classNames?: string;
	steps: StepType[];
	nextText?: string;
	enableNext?: boolean;
	isLoading?: boolean;
	onNext: () => void;
}

const Stepper: FunctionComponent<StepperProps> = (props) => {

	const { t } = useTranslation();

	const activeIndex = _.findIndex(props.steps, x => x.current) + 1;

	return (
		<div className={mergeClasses(style.container, props.classNames)}>
			<ol className={style.stepper}>
				{
					props.steps.map((step, index) => (
						<Step
							key={index}
							step={step}
							position={index + 1}
							current={activeIndex}
						/>
					))
				}
			</ol>
			<ActionButton
				classNames={style.next}
				text={props.nextText ?? t('Vai al passaggio successivo')}
				onClick={props.onNext}
				isLoading={props.isLoading}
				disabled={typeof props.enableNext != 'undefined' && !props.enableNext}
			/>
		</div>
	);
};

interface StepProps {
	step: StepType;
	position: number;
	current: number;
}

const Step: FunctionComponent<StepProps> = ({ step, position, current }) => {

	const navigate = useStrictNavigate();

	return (
		<li
			className={mergeClasses(style.step, { ['cursor-pointer']: !isEmpty(step.route) })}
			onClick={UnwrapElementCallback(() => {
				if (position < current) {
					navigate(position - current);
				} else {
					navigate(step.route);
				}
			})}
		>
			<div
				className={mergeClasses(
					style.position,
					{
						[style.completed]: step.completed,
						[style.current]: step.current,
						[style.disabled]: !step.current && !step.completed
					}
				)}
			>
				{
					!step.completed
						? position
						: <ProImage src={Check}/>
				}
			</div>
			{step.description}
		</li>
	);
};

export default Stepper;
