import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { apiCompleted, apiError, apiFailed, apiInit, getApiDefaultState } from '../../../../../network/helpers';
import _ from 'lodash';
import { IMainMemberFactoryState, MAIN_MEMBER_FACTORY_CLEAR_FORM, MAIN_MEMBER_FACTORY_UPDATE_FORM, MainMemberFactoryActions } from './types';
import {
	NETWORK_MAIN_MEMBER_FACTORY_CLEAR,
	NETWORK_MAIN_MEMBER_FACTORY_COMPLETED,
	NETWORK_MAIN_MEMBER_FACTORY_ERROR,
	NETWORK_MAIN_MEMBER_FACTORY_FAILED,
	NETWORK_MAIN_MEMBER_FACTORY_INIT
} from '../../../network/mainMember/factory/types';
import { ROUTER_HISTORY_CHANGED } from '../../../../../router/routerHistory/store/types';

const initialState = Object.freeze<IMainMemberFactoryState>({
	api: getApiDefaultState(),
	data: undefined,
	form: {
		isDirty: false,
		fields: {
			secondaryEmail: '',
			secondaryPhone: '',
			shipment: 'on-site',
			shipmentAddress: '',
			shipmentZipCode: '',
			shipmentCity: '',
			shipmentProvince: '',
			shipmentNotes: '',
			identityDocument: {
				data: '',
				name: '',
				mimeType: '',
				extension: '',
				isAttached: false
			},
			fiscalCodeDocument: {
				data: '',
				name: '',
				mimeType: '',
				extension: '',
				isAttached: false
			},
			passportPhotoDocument: {
				data: '',
				name: '',
				mimeType: '',
				extension: '',
				isAttached: false
			}
		}
	}
});

const MainMemberFactoryReducer: Reducer<IMainMemberFactoryState, MainMemberFactoryActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IMainMemberFactoryState>) => {
		switch (action.type) {
			case ROUTER_HISTORY_CHANGED: {
				return initialState;
			}
			case NETWORK_MAIN_MEMBER_FACTORY_INIT: {
				apiInit(draft.api);
				break;
			}
			case NETWORK_MAIN_MEMBER_FACTORY_COMPLETED: {

				const { status, message, data } = action.payload;

				apiCompleted(draft.api, status, message);
				draft.data = data;

				// Form
				draft.form.isDirty = false;
				break;
			}
			case NETWORK_MAIN_MEMBER_FACTORY_FAILED: {

				const { status, message, validationErrors } = action.payload;

				apiFailed(draft.api, status, message, validationErrors);
				break;
			}
			case NETWORK_MAIN_MEMBER_FACTORY_ERROR: {
				apiError(draft.api);
				break;
			}
			case NETWORK_MAIN_MEMBER_FACTORY_CLEAR: {
				draft.api = initialState.api;
				break;
			}

			case MAIN_MEMBER_FACTORY_UPDATE_FORM: {
				draft.form.isDirty = true;
				_.merge(draft.form.fields, action.payload);
				break;
			}
			case MAIN_MEMBER_FACTORY_CLEAR_FORM: {
				draft.form.isDirty = false;
				_.assign(draft.form.fields, initialState.form.fields, action.payload);
				break;
			}
		}
	});
};

export default MainMemberFactoryReducer;
