import React, { FunctionComponent } from 'react';
import style from './style/CardRequestForm.module.scss';
import { mergeClasses, MockCallback } from '../../../../helpers/utils';
import ResponsiveContainer from '../../../shared/ui/responsiveContainer';
import Content from '../../../shared/ui/content';
import Input from '../../../shared/ui/forms/input';
import { useTranslation } from 'react-i18next';
import InputSelect from '../../../shared/ui/forms/input-select';
import InputRadio from '../../../shared/ui/forms/input-radio';
import { useMount, usePreload, useStrictNavigate } from '../../../system/hooks';
import Stepper from '../../ui/stepper';
import DocumentPicker from '../../ui/documentPicker';
import PassportPhotoPicker from '../../ui/passportPhotoPicker';
import { ShipmentType } from '../../types/shipmentType';
import { IMainMemberFormStoreProps } from './store/props';
import { MainMemberStatus } from '../../types/mainMemberStatus';
import { DateTime } from 'luxon';
import Collapsible from '../../ui/collapsible';
import Textarea from '../../../shared/ui/forms/textarea';
import { isRequestSuccessful } from '../../../../network/utils';
import { getFamilyMembersRoute } from '../../CardRequestModuleRouteComposers';

interface Props extends IMainMemberFormStoreProps {}

const STATUS: { key: MainMemberStatus; value: string; }[] = [
	{ key: 'in-service', value: 'In Servizio' },
	{ key: 'retired', value: 'In Pensione' }
];

const CardRequestForm: FunctionComponent<Props> = (props) => {

	const { factory, update } = props;

	const { t } = useTranslation();
	const navigate = useStrictNavigate();

	usePreload([
		[ factory.api, () => props.notifyNetworkMainMemberFactoryRequested() ]
	]);

	useMount(() => {
		if (!isRequestSuccessful(factory.api) || !factory.data) return;
		props.notifyMainMemberFactoryUpdateForm({
			secondaryEmail: factory.data.caponucleoDettagli?.secondaryEmail ?? '',
			secondaryPhone: factory.data.caponucleoDettagli?.secondaryPhone ?? '',
			shipment: factory.data.caponucleoDettagli?.shipment as ShipmentType ?? 'on-site'
		});
	}, [ factory.api.receivedAt ]);

	useMount(() => {
		if (isRequestSuccessful(update.api)) {
			navigate(getFamilyMembersRoute());
		}
	}, [ update.api.receivedAt ]);

	const onSubmit = () => {
		if (update.api.isLoading) return;
		props.notifyNetworkMainMemberPushFactoryRequested({
			data: {
				id: factory.data!.id,
				secondaryEmail: factory.form.fields.secondaryEmail,
				secondaryPhone: factory.form.fields.secondaryPhone,
				shipment: factory.form.fields.shipment,
				shipmentAddress: factory.form.fields.shipmentAddress,
				shipmentZipCode: factory.form.fields.shipmentZipCode,
				shipmentCity: factory.form.fields.shipmentCity,
				shipmentProvince: factory.form.fields.shipmentProvince,
				shipmentNotes: factory.form.fields.shipmentNotes,
				identityDocument: factory.form.fields.identityDocument.data,
				fiscalCodeDocument: factory.form.fields.fiscalCodeDocument.data,
				passportPhotoDocument: factory.form.fields.passportPhotoDocument.data
			}
		});
	};

	if (!factory.data) return null;

	return (
		<ResponsiveContainer classNames={style.container}>

			<Content>

				<Stepper
					steps={[
						{ current: true, completed: false, description: t('I tuoi dati') },
						{ current: false, completed: false, description: t('I tuoi nuclei familiari') },
						{ current: false, completed: false, description: t('Riepilogo e pagamento') }
					]}
					isLoading={update.api.isLoading}
					onNext={onSubmit}
				/>

				<form
					className={'w-100 mt-1'}
					onSubmit={(e) => e.preventDefault()}
					onSubmitCapture={(e) => e.preventDefault()}
				>

					<p>{t('Ti diamo il benvenuto nella procedura di attivazione della Carta Esercito. Controlla i tuoi dati e inserisci, laddove necessario, quelli mancanti.')}</p>

					<div className={style.formSection}>

						<h4 className={'mb-half'}>{t('I miei dati')}</h4>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'name'}
									label={t('Nome')}
									value={factory.data.caponucleo.name}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'surname'}
									label={t('Cognome')}
									value={factory.data.caponucleo.surname}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'birth_date'}
									label={t('Data di nascita')}
									type={'date'}
									value={DateTime.fromISO(factory.data.caponucleo.birthDate).toFormat('yyyy-MM-dd')}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'birth_place'}
									label={t('Luogo di nascita')}
									value={factory.data.caponucleo.birthPlace}
									disabled={true}
								/>
							</div>
						</div>

						<div className={style.formFieldsRow}>
							<div className={mergeClasses(style.field, style.large)}>
								<Input
									name={'street'}
									label={t('Via/Piazza Residenza')}
									value={factory.data.caponucleo.street}
									disabled={true}
								/>
							</div>
							<div className={mergeClasses(style.field, style.small)}>
								<Input
									name={'zip'}
									label={t('CAP')}
									value={factory.data.caponucleo.zipCode}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'city'}
									label={t('Comune')}
									value={factory.data.caponucleo.province}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'province'}
									label={t('Provincia')}
									value={factory.data.caponucleo.province}
									disabled={true}
								/>
							</div>
						</div>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<InputSelect
									name={'status'}
									label={t('Posizione lavorativa')}
									placeholder={t('Seleziona una posizione...')}
									value={factory.data.caponucleo.status}
									defaultValue={STATUS[0].key}
									options={STATUS.map(({ key, value }) => ({ label: value, value: key }))}
									searchable={false}
									disabled={true}
									onSelect={MockCallback}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'administration'}
									label={t('Amministrazione di appartenenza')}
									value={factory.data.caponucleo.administration}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'department'}
									label={t('Ente di appartenenza')}
									value={factory.data.caponucleo.department}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'grade'}
									label={t('Grado/Qualifica')}
									value={factory.data.caponucleo.grade}
									disabled={true}
								/>
							</div>
						</div>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'primary_phone'}
									label={t('Telefono')}
									value={factory.data.caponucleo.primaryPhone}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'primary_email'}
									type={'email'}
									label={t('Mail')}
									value={factory.data.caponucleo.primaryEmail}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'nationality'}
									label={t('Nazionalità')}
									value={factory.data.caponucleo.nationality}
									disabled={true}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'fiscal_code'}
									label={t('Codice Fiscale')}
									value={factory.data.caponucleo.fiscalCode}
									disabled={true}
								/>
							</div>
						</div>

					</div>

					<div className={style.formSection}>

						<h4 className={'mb-half'}>{t('Contatti alternativi')}</h4>

						<div className={style.formFieldsRow}>
							<div className={style.field}>
								<Input
									name={'secondary_email'}
									type={'email'}
									label={t('Mail')}
									value={factory.form.fields.secondaryEmail}
									onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
										secondaryEmail: e.target.value
									})}
								/>
							</div>
							<div className={style.field}>
								<Input
									name={'secondary_phone'}
									label={t('Telefono')}
									value={factory.form.fields.secondaryPhone}
									onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
										secondaryPhone: e.target.value
									})}
								/>
							</div>
						</div>

					</div>

					<div className={style.formSection}>

						<h4 className={'mb-half'}>{t('Come vuoi ricevere la tua Carta Esercito?')}</h4>

						<div className={'w-100 mt-1'}>
							<InputRadio
								name={'shipment'}
								optionClassnames={style.shipmentOption}
								labelClassnames={style.shipmentOptionLabel}
								value={factory.form.fields.shipment}
								options={[
									{
										value: 'on-site',
										label: t('Ritiro in sede (Stato maggiore dell’Esercito - V Reparto Affari Generali - Ufficio Affari Generali')
									},
									{
										value: 'at-home',
										label: t('Spedizione raccomandata Chrono (4,50€)')
									}
								] as { value: ShipmentType; label: string; }[]}
								onChange={({ value }) => props.notifyMainMemberFactoryUpdateForm({ shipment: value as ShipmentType })}
							/>
							<Collapsible
								isOpen={factory.form.fields.shipment == 'at-home'}
								onClose={() => props.notifyMainMemberFactoryUpdateForm({
									shipmentAddress: '',
									shipmentZipCode: '',
									shipmentCity: '',
									shipmentProvince: '',
									shipmentNotes: ''
								})}
							>
								<div className={style.shipmentForm}>
									<div className={style.formFieldsRow}>
										<div className={mergeClasses(style.field, style.large)}>
											<Input
												name={'shipment_address'}
												label={t('Indirizzo')}
												value={factory.form.fields.shipmentAddress}
												onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
													shipmentAddress: e.target.value
												})}
											/>
										</div>
										<div className={mergeClasses(style.field, style.small)}>
											<Input
												name={'shipment_zip'}
												label={t('CAP')}
												value={factory.form.fields.shipmentZipCode}
												onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
													shipmentZipCode: e.target.value
												})}
											/>
										</div>
										<div className={style.field}>
											<Input
												name={'shipment_city'}
												label={t('Comune')}
												value={factory.form.fields.shipmentCity}
												onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
													shipmentCity: e.target.value
												})}
											/>
										</div>
										<div className={style.field}>
											<Input
												name={'shipment_province'}
												label={t('Provincia')}
												value={factory.form.fields.shipmentProvince}
												onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
													shipmentProvince: e.target.value
												})}
											/>
										</div>
									</div>
									<div className={style.formFieldsRow}>
										<Textarea
											name={'shipment_notes'}
											label={t('Note')}
											value={factory.form.fields.shipmentNotes}
											onChange={(e) => props.notifyMainMemberFactoryUpdateForm({
												shipmentNotes: e.target.value
											})}
										/>
									</div>
								</div>
							</Collapsible>
						</div>

					</div>

					<div className={style.formSection}>

						<h4 className={'mb-half'}>{t('Carica i documenti')}</h4>

						<div className={'w-100 mt-1'}>
							<DocumentPicker
								title={t('Documento di identità')}
								subtitle={t('(peso massimo: 8Mb; formato: pdf)')}
								document={factory.form.fields.identityDocument}
								onFilePicked={(document) => props.notifyMainMemberFactoryUpdateForm({
									identityDocument: document
								})}
								onClear={() => props.notifyMainMemberFactoryUpdateForm({
									identityDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

						<div className={'w-100 mt-1'}>
							<DocumentPicker
								title={t('Codice fiscale')}
								subtitle={t('(peso massimo: 8Mb; formato: pdf)')}
								document={factory.form.fields.fiscalCodeDocument}
								onFilePicked={(document) => props.notifyMainMemberFactoryUpdateForm({
									fiscalCodeDocument: document
								})}
								onClear={() => props.notifyMainMemberFactoryUpdateForm({
									fiscalCodeDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

						<div className={'w-100 mt-1'}>
							<PassportPhotoPicker
								photo={factory.form.fields.passportPhotoDocument}
								onPhotoPicked={(document) => props.notifyMainMemberFactoryUpdateForm({
									passportPhotoDocument: document
								})}
								onClear={() => props.notifyMainMemberFactoryUpdateForm({
									passportPhotoDocument: {
										data: '',
										name: '',
										mimeType: '',
										extension: '',
										isAttached: false
									}
								})}
							/>
						</div>

					</div>

				</form>
			</Content>
		</ResponsiveContainer>
	);
};

export default CardRequestForm;
