export const NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED = 'NETWORK_MAIN_MEMBER_PUSH_FACTORY_REQUESTED';
export const NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT = 'NETWORK_MAIN_MEMBER_PUSH_FACTORY_INIT';
