import React, { FunctionComponent } from 'react';
import style from './style/ResponsiveContainer.module.scss';
import { mergeClasses } from '../../../../helpers/utils';

interface Props {
	classNames?: string;
}

const ResponsiveContainer: FunctionComponent<Props> = (props) => {
	return (
		<div className={mergeClasses(style.root, props.classNames)}>
			<div className={style.container}>
				{props.children}
			</div>
		</div>
	);
};

export default ResponsiveContainer;
