import _ from 'lodash';
import { isEmpty } from './utils';

class Validator {

	private constructor() {}

	static number(value: number | string | null | undefined): number {
		try {
			if (Validator.isNumeric(value)) {
				return typeof value == 'number' ? value : parseInt(value as string);
			}
		} catch (e) {}
		return 0;
	}

	static float(value: number | string | null | undefined): number {
		try {
			if (Validator.isNumeric(value)) {
				return typeof value == 'number' ? value : parseFloat(value as string);
			}
		} catch (e) {}
		return 0.0;
	}

	static string(value: string | null | undefined): string {
		return !isEmpty(value) ? value! : '';
	}

	static isNumeric(value: number | string | null | undefined): boolean {
		try { return typeof value == 'number' || (typeof value == 'string' && !_.isNaN(parseFloat(value))); } catch (e) { return false; }
	}

	static fiscalCode(value: string | null | undefined): boolean {
		if (isEmpty(value)) return false;
		const regex = /^(?:[A-Z][AEIOU][AEIOUX]|[AEIOU]X{2}|[B-DF-HJ-NP-TV-Z]{2}[A-Z]){2}(?:[\dLMNP-V]{2}(?:[A-EHLMPR-T](?:[04LQ][1-9MNP-V]|[15MR][\dLMNP-V]|[26NS][0-8LMNP-U])|[DHPS][37PT][0L]|[ACELMRT][37PT][01LM]|[AC-EHLMPR-T][26NS][9V])|(?:[02468LNQSU][048LQU]|[13579MPRTV][26NS])B[26NS][9V])(?:[A-MZ][1-9MNP-V][\dLMNP-V]{2}|[A-M][0L](?:[1-9MNP-V][\dLMNP-V]|[0L][1-9MNP-V]))[A-Z]$/;
		return regex.test(value!.toUpperCase());
	}
}

export default Validator;
