import React, { FunctionComponent, useCallback } from 'react';
import i18n from 'i18next';
import HttpApi from 'i18next-http-backend';
import { initReactI18next } from 'react-i18next';
import { DateTime, DateTimeFormatOptions, LocaleOptions } from 'luxon';
import { canUseDOM, isEmpty } from '../../../../helpers/utils';
import { useImmediateMount, useMount } from '../../hooks';
import DefaultHeaders from '../../../../network/defaultHeaders';

type Locale = { code: string; timezone: string; };
type DateFormatter = (rawDate: DateTime | string | undefined, formatOptions: LocaleOptions & DateTimeFormatOptions) => string;
type LocalizationHook = {
	code: string;
	timezone: string;
	defaultCode: string;
	defaultTimezone: string;
	changeLocale: (code: string) => void;
	changeTimezone: (timezone: string) => void;
	translate: (key: string) => void;
	formatDate: DateFormatter;
	createDateFromUnix: (seconds: number) => string;
};

export interface LocalizationProviderProps {
	backendUrl: string;
	defaultCode: string;
	defaultTimezone: string;
	getCurrentLocale: () => Locale;
}

const LocalizationProvider: FunctionComponent<LocalizationProviderProps> = ({ children, ...props }) => {

	const { backendUrl, defaultCode, defaultTimezone, getCurrentLocale } = props;

	const { code, timezone } = getCurrentLocale();

	Localization.code = code || defaultCode;
	Localization.timezone = timezone || defaultTimezone;
	Localization.defaultCode = defaultCode;
	Localization.defaultTimezone = defaultTimezone;

	const { changeLocale } = useLocalization();

	useImmediateMount(() => {
		i18n
			.use(HttpApi)
			.use(initReactI18next)
			.init(
				{
					backend: {
						loadPath: (lng, ns) => {
							return `${backendUrl}/api/v1/control/translations/${lng[0].toLowerCase()}/${ns[0].toLowerCase()}`;
						},
						customHeaders: {
							[DefaultHeaders.ACCEPT]: 'application/json',
							[DefaultHeaders.CONTENT_TYPE]: 'application/json',
							[DefaultHeaders.CONTENT_LANGUAGE]: code.toLowerCase() || defaultCode.toLowerCase()
						}
					},
					detection: {
						order: [ 'localStorage' ],
						lookupLocalStorage: 'language',
						caches: [ 'localStorage' ]
					},
					debug: false,
					fallbackLng: defaultCode.toLowerCase(),
					nonExplicitSupportedLngs: false,
					load: 'currentOnly',
					ns: [ 'translation' ],
					defaultNS: 'translation',
					interpolation: {
						escapeValue: false
					},
					react: {
						// TODO: set to true
						useSuspense: false
					}
				},
				(error, t) => {
					if (error) {
						changeLocale(defaultCode);
						console.error(`i18n error: ${error}`);
						return;
					}
					if (!isEmpty(code)) {
						changeLocale(code);
					}
				}
			);
	});

	const updateDocumentLang = useCallback((code) => {
		if (!canUseDOM()) return;
		document.documentElement.setAttribute('lang', code);
	}, []);

	useMount(() => {
		i18n.on('languageChanged', updateDocumentLang);
		return () => i18n.off('languageChanged', updateDocumentLang);
	});

	return (<>{children}</>);
};

const changeLocale = async (code: string): Promise<void> => {
	try {
		await i18n.changeLanguage(code);
		Localization.code = code;
	} catch (e) {
		console.error(e);
	}
};

const changeTimezone = (timezone: string): void => {
	Localization.timezone = timezone;
};

export const translate = (key: string): string => {
	return i18n.t(key);
};

const formatDate = (rawDate: DateTime | string | undefined, formatOptions: LocaleOptions & DateTimeFormatOptions): string => {

	const { code, timezone } = Localization;

	if (!code || !timezone || !rawDate) return '';

	const date: DateTime = typeof rawDate === 'string'
		? DateTime.fromISO(rawDate as string, {
			setZone: true,
			zone: 'Etc/UTC'
		})
		: rawDate;

	return date
		.setLocale(code)
		.setZone(timezone)
		.toLocaleString(formatOptions);
};

const createDateFromUnix = (seconds: number): string => {
	return DateTime.fromSeconds(seconds).toISODate();
};

let Localization: Partial<LocalizationHook> = {
	changeLocale,
	changeTimezone,
	translate,
	formatDate,
	createDateFromUnix
};

export const getLocalization = (): Readonly<LocalizationHook> => {
	return Localization as Required<LocalizationHook>;
};

export const useLocalization = (): Readonly<LocalizationHook> => {
	return Localization as Required<LocalizationHook>;
};

export default LocalizationProvider;
