import { Draft } from 'immer';
import { getUnixMillsTimestamp } from '../helpers/utils';
import { IApiState } from './models/IApiState';
import { IValidationErrors } from './models/IValidationErrors';
import { STATUS_CODE_DEFAULT } from './httpStatus';

/*
|--------------------------------------------------------------------------
| State Generators
|--------------------------------------------------------------------------
*/
export const getApiDefaultState = (): IApiState['api'] => {
	return {
		status: STATUS_CODE_DEFAULT,
		isLoading: false,
		isFailed: false,
		isError: false,
		sentAt: 0,
		receivedAt: 0,
		message: '',
		validationErrors: {}
	};
};

/*
|--------------------------------------------------------------------------
| API Lifecycle
|--------------------------------------------------------------------------
*/
export const apiInit = <T extends IApiState['api']>(draft: Draft<T>) => {
	draft.isLoading = true;
	draft.validationErrors = {};
	draft.sentAt = getUnixMillsTimestamp();
};

export const apiCompleted = <T extends IApiState['api']>(draft: Draft<T>, status: number, message?: string) => {
	draft.isLoading = false;
	draft.isFailed = false;
	draft.isError = false;
	draft.status = status;
	draft.message = message ?? '';
	draft.receivedAt = getUnixMillsTimestamp();
};

export const apiFailed = <T extends IApiState['api']>(draft: Draft<T>, status: number, message?: string, validationErrors?: IValidationErrors) => {
	draft.isLoading = false;
	draft.isFailed = true;
	draft.isError = false;
	draft.status = status;
	draft.message = message ?? '';
	draft.validationErrors = validationErrors ?? {};
	draft.receivedAt = getUnixMillsTimestamp();
};

export const apiError = <T extends IApiState['api']>(draft: Draft<T>) => {
	draft.isLoading = false;
	draft.isFailed = false;
	draft.isError = true;
};
