import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { ISystemLocaleState, SYSTEM_LOCALE_CHANGED, SystemLocaleActions } from './types';

const initialState = Object.freeze<ISystemLocaleState>({
	code: ''
});

const SystemLocaleReducer: Reducer<ISystemLocaleState, SystemLocaleActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<ISystemLocaleState>) => {
		switch (action.type) {
			case SYSTEM_LOCALE_CHANGED: {
				draft.code = action.payload.code;
			}
		}
	});
};

export default SystemLocaleReducer;
