import React, { FunctionComponent } from 'react';
import style from './style/ActionButton.module.scss';
import { mergeClasses, UnwrapElementCallback } from '../../../../helpers/utils';
import Spinner from '../../../shared/ui/spinner';

interface Props {
	text: string;
	classNames?: string;
	onClick: () => void;
	icon?: JSX.Element;
	disabled?: boolean;
	isLoading?: boolean;
}

const ActionButton: FunctionComponent<Props> = (props) => {
	return (
		<div
			className={mergeClasses(style.container, { [style.disabled]: props.disabled, [style.loading]: props.isLoading }, props.classNames)}
			onClick={UnwrapElementCallback(() => {
				if (props.disabled || props.isLoading) return;
				props.onClick();
			})}
		>
			{
				!props.isLoading &&
				<>
					{props.text}
					{
						props.icon &&
						<div className={'display-flex flex-column justify-center align-items-center ml-half'}>
							{props.icon}
						</div>
					}
				</>
			}
			{props.isLoading && <Spinner/>}
		</div>
	);
};

export default ActionButton;
