import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';

const withoutTrailingSlash = <T extends object>(Component: React.FunctionComponent<T>) => (props: T) => {

	const location = useLocation();

	if (location.pathname.endsWith('/')) {
		return (
			<Navigate to={location.pathname.slice(0, -1)} replace={true}/>
		);
	}

	return (<Component {...props}/>);
};

export default withoutTrailingSlash;
