import { IApiRequest } from '../../../../../network/models/IApiRequest';
import { StoreAction } from '../../../../../store/models/actions/storeAction';
import { BasicStoreAction } from '../../../../../store/models/actions/basicStoreAction';
import { IApiPayload, Unsuccessful } from '../../../../../network/models/IApiPayload';
import { IApiResponse } from '../../../../../network/models/IApiResponse';
import { IMainMemberDetails, IMainMemberPersonalData } from '../../../models/IMainMember';

export type IMainMemberFactoryRequest = IApiRequest

export type IMainMemberFactoryResponse = IApiResponse<{
	id: number;
	userId: string;
	caponucleo: IMainMemberPersonalData;
	caponucleoDettagli: IMainMemberDetails;
}>

export type IMainMemberFactoryPayload = IApiPayload<IMainMemberFactoryResponse['data']>

export const NETWORK_MAIN_MEMBER_FACTORY_REQUESTED = 'NETWORK_MAIN_MEMBER_FACTORY_REQUESTED';
export const NETWORK_MAIN_MEMBER_FACTORY_INIT = 'NETWORK_MAIN_MEMBER_FACTORY_INIT';
export const NETWORK_MAIN_MEMBER_FACTORY_COMPLETED = 'NETWORK_MAIN_MEMBER_FACTORY_COMPLETED';
export const NETWORK_MAIN_MEMBER_FACTORY_FAILED = 'NETWORK_MAIN_MEMBER_FACTORY_FAILED';
export const NETWORK_MAIN_MEMBER_FACTORY_ERROR = 'NETWORK_MAIN_MEMBER_FACTORY_ERROR';
export const NETWORK_MAIN_MEMBER_FACTORY_CLEAR = 'NETWORK_MAIN_MEMBER_FACTORY_CLEAR';

export type INotifyNetworkMainMemberFactoryRequested = StoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_REQUESTED, IMainMemberFactoryRequest>
export type INotifyNetworkMainMemberFactoryInit = BasicStoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_INIT>
export type INotifyNetworkMainMemberFactoryCompleted = StoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_COMPLETED, IMainMemberFactoryPayload>
export type INotifyNetworkMainMemberFactoryFailed = StoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_FAILED, Unsuccessful<IMainMemberFactoryPayload>>
export type INotifyNetworkMainMemberFactoryError = BasicStoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_ERROR>
export type INotifyNetworkMainMemberFactoryClear = BasicStoreAction<typeof NETWORK_MAIN_MEMBER_FACTORY_CLEAR>

export type MainMemberFactoryNetworkActions =
	INotifyNetworkMainMemberFactoryRequested
	| INotifyNetworkMainMemberFactoryInit
	| INotifyNetworkMainMemberFactoryCompleted
	| INotifyNetworkMainMemberFactoryFailed
	| INotifyNetworkMainMemberFactoryError
	| INotifyNetworkMainMemberFactoryClear
	;
