import './prototypes';
import './style/index.scss';

import React, { FunctionComponent, Suspense } from 'react';
import ReactDOM from 'react-dom';
import { HelmetProvider } from 'react-helmet-async';
import { Provider } from 'react-redux';
import StoreManager from './store/storeManager';
import { ConnectionCore } from './network/defaultConnection';
import reportWebVitals from './reportWebVitals';
import Router from './router/Router';
import StrictMode from './modules/system/utilities/strictMode';
import { BACKEND_URL, DEFAULT_CODE_FALLBACK, DEFAULT_TIMEZONE_FALLBACK, DEVELOPMENT_MODE } from './constants';
import ReactModal from 'react-modal';
import LocalizationProvider from './modules/system/providers/localization/localization';
import createCache from '@emotion/cache'
import { CacheProvider } from '@emotion/react';

export const init = (domElement:HTMLDivElement) => {
	initApp(domElement);
};

const initApp = (domElement: HTMLDivElement) => {

	// Setting modal anchor
	ReactModal.setAppElement('#app-root');

	// Redux store
	const storeInstance = new StoreManager({
		logRootState: false
	});

	// Network
	ConnectionCore.init({
		baseUrl: `${BACKEND_URL}/api/`
	});

	const Localization: FunctionComponent = ({ children }) => {

		const getCurrentLocale = () => {

			const code = DEFAULT_CODE_FALLBACK;
			const timezone = DEFAULT_TIMEZONE_FALLBACK;

			return { code, timezone };
		};

		return (
			<Suspense fallback={null}>
				<LocalizationProvider
					backendUrl={BACKEND_URL}
					defaultCode={DEFAULT_CODE_FALLBACK}
					defaultTimezone={DEFAULT_TIMEZONE_FALLBACK}
					getCurrentLocale={getCurrentLocale}
				>
					{children}
				</LocalizationProvider>
			</Suspense>
		);
	};

	const shadowrootCache = createCache({
		key: 'shadowroot-cache',
		container: domElement
	})

	const Root = () => (
		<StrictMode enable={false}>
			<CacheProvider value={shadowrootCache}>
				<Provider store={storeInstance.getStore()}>
					<HelmetProvider>
						<Router/>
					</HelmetProvider>
				</Provider>
			</CacheProvider>
		</StrictMode>
	);

	const RenderApp = (renderer: ReactDOM.Renderer) => {
		renderer(<Root/>, domElement);
	};

	RenderApp(ReactDOM.render);
};

