import React, { FunctionComponent } from 'react';
import ReactModal from 'react-modal';
import { mergeClasses } from '../../../../helpers/utils';

interface Props extends Omit<ReactModal.Props, 'isOpen' | 'className'> {
	isOpen?: boolean;
	className?: string;
}

const Modal: FunctionComponent<Props> = (props) => {

	if (typeof props.isOpen != 'undefined' && !props.isOpen) return null;

	return (
		<ReactModal
			shouldCloseOnEsc={false}
			shouldCloseOnOverlayClick={false}
			shouldFocusAfterRender={true}
			{...props}
			isOpen={true}
			className={mergeClasses('modal__container', props.className)}
			/* eslint-disable-next-line */
			/* @ts-ignore */
			parentSelector={()=> document.getElementById('app-root').shadowRoot}
		/>
	);
};

export default Modal;

