import React from 'react';
import { UnwrapElementCallback } from '../../../../helpers/utils';
import bytes, { Unit } from 'bytes';

interface Props {
	mimeTypes: string;
	maxSize?: number | `${number}${Unit}`;
	onFilePicked: (file: File, mimeType: string, extension: string) => void;
	onFileRejected?: () => void;
}

const FilePicker = React.forwardRef<HTMLInputElement, Props>((props, ref) => {
	return (
		<input
			ref={ref}
			className={'display-none'}
			type={'file'}
			accept={props.mimeTypes || '*'}
			multiple={false}
			onChange={UnwrapElementCallback((e) => {
				const file = e.currentTarget.files[0];
				if (typeof props.maxSize != 'undefined' && file.size > bytes.parse(props.maxSize)) {
					props.onFileRejected && props.onFileRejected();
					return;
				}
				const extension = file.name.substring(file.name.lastIndexOf('.') + 1).toLowerCase();
				props.onFilePicked(file, file.type, extension);
			})}
		/>
	);
});

export default FilePicker;
