import { Reducer } from 'redux';
import produce, { Draft } from 'immer';
import { FamilyListingActions, IFamilyListingState } from './types';
import { apiCompleted, apiError, apiFailed, apiInit, getApiDefaultState } from '../../../../../network/helpers';
import {
	NETWORK_FAMILY_LISTING_CLEAR,
	NETWORK_FAMILY_LISTING_COMPLETED,
	NETWORK_FAMILY_LISTING_ERROR,
	NETWORK_FAMILY_LISTING_FAILED,
	NETWORK_FAMILY_LISTING_INIT
} from '../../../network/family/listing/types';

const initialState = Object.freeze<IFamilyListingState>({
	api: getApiDefaultState(),
	data: {
		main: [],
		externals: []
	}
});

const FamilyListingReducer: Reducer<IFamilyListingState, FamilyListingActions> = (state = initialState, action) => {
	return produce(state, (draft: Draft<IFamilyListingState>) => {
		switch (action.type) {
			// TODO: uncomment
			// case ROUTER_HISTORY_CHANGED: {
			// 	return initialState;
			// }
			case NETWORK_FAMILY_LISTING_INIT: {
				apiInit(draft.api);
				break;
			}
			case NETWORK_FAMILY_LISTING_COMPLETED: {

				const { status, message, data } = action.payload;

				apiCompleted(draft.api, status, message);
				draft.data = data;
				break;
			}
			case NETWORK_FAMILY_LISTING_FAILED: {

				const { status, message, validationErrors } = action.payload;

				apiFailed(draft.api, status, message, validationErrors);
				break;
			}
			case NETWORK_FAMILY_LISTING_ERROR: {
				apiError(draft.api);
				break;
			}
			case NETWORK_FAMILY_LISTING_CLEAR: {
				draft.api = initialState.api;
				break;
			}
		}
	});
};

export default FamilyListingReducer;
