import { IApiRequest } from '../../../../../network/models/IApiRequest';
import { StoreAction } from '../../../../../store/models/actions/storeAction';
import { BasicStoreAction } from '../../../../../store/models/actions/basicStoreAction';
import { IApiPayload, Unsuccessful } from '../../../../../network/models/IApiPayload';
import { IApiResponse } from '../../../../../network/models/IApiResponse';

export type IMemberCreateRequest = IApiRequest<{
	nucleoId: number | null;
	name: string;
	surname: string;
	birthDate: string;
	birthPlace: string;
	fiscalCode: string;
	nationality: string;
	primaryEmail: string;
	primaryPhone: string;
	relationship: string;
	isDefenseDepartmentEmployee: boolean;
	identityDocument: string;
	fiscalCodeDocument: string;
	passportPhotoDocument: string;
}>

export type IMemberCreateResponse = IApiResponse

export type IMemberCreatePayload = IApiPayload

export const NETWORK_MEMBER_CREATE_REQUESTED = 'NETWORK_MEMBER_CREATE_REQUESTED';
export const NETWORK_MEMBER_CREATE_INIT = 'NETWORK_MEMBER_CREATE_INIT';
export const NETWORK_MEMBER_CREATE_COMPLETED = 'NETWORK_MEMBER_CREATE_COMPLETED';
export const NETWORK_MEMBER_CREATE_FAILED = 'NETWORK_MEMBER_CREATE_FAILED';
export const NETWORK_MEMBER_CREATE_ERROR = 'NETWORK_MEMBER_CREATE_ERROR';
export const NETWORK_MEMBER_CREATE_CLEAR = 'NETWORK_MEMBER_CREATE_CLEAR';

export type INotifyNetworkMemberCreateRequested = StoreAction<typeof NETWORK_MEMBER_CREATE_REQUESTED, IMemberCreateRequest>
export type INotifyNetworkMemberCreateInit = BasicStoreAction<typeof NETWORK_MEMBER_CREATE_INIT>
export type INotifyNetworkMemberCreateCompleted = StoreAction<typeof NETWORK_MEMBER_CREATE_COMPLETED, IMemberCreatePayload>
export type INotifyNetworkMemberCreateFailed = StoreAction<typeof NETWORK_MEMBER_CREATE_FAILED, Unsuccessful<IMemberCreatePayload>>
export type INotifyNetworkMemberCreateError = BasicStoreAction<typeof NETWORK_MEMBER_CREATE_ERROR>
export type INotifyNetworkMemberCreateClear = BasicStoreAction<typeof NETWORK_MEMBER_CREATE_CLEAR>

export type MemberCreateNetworkActions =
	INotifyNetworkMemberCreateRequested
	| INotifyNetworkMemberCreateInit
	| INotifyNetworkMemberCreateCompleted
	| INotifyNetworkMemberCreateFailed
	| INotifyNetworkMemberCreateError
	| INotifyNetworkMemberCreateClear
	;
