import React, { FunctionComponent, useState } from 'react';
import style from './style/Checkout.module.scss';
import ResponsiveContainer from '../../../shared/ui/responsiveContainer';
import Stepper from '../../ui/stepper';
import Content from '../../../shared/ui/content';
import { useTranslation } from 'react-i18next';
import { ICheckoutStoreProps } from './store/props';
import _ from 'lodash';
import { sprintf } from 'sprintf-js';
import Tag from '../../ui/tag';
import { isEmpty, mergeClasses } from '../../../../helpers/utils';
import ProImage from '../../../shared/ui/proImage';
import { ExternalLink } from '../../../../assets/images/externalLink';
import { Info, InfoOutline } from '../../../../assets/images/info';
import Input from '../../../shared/ui/forms/input';
import DocumentPicker from '../../ui/documentPicker';
import { IFormFile } from '../../../../store/models/IFormFile';
import { usePreload, useStrictNavigate } from '../../../system/hooks';
import { getCardRequestFormRoute, getFamilyMembersRoute, getOrderConfirmationRoute } from '../../CardRequestModuleRouteComposers';
import { isRequestFreshOrPending } from '../../../../network/utils';

interface Props extends ICheckoutStoreProps {}

const RELATIONSHIP: { key: string; value: string; }[] = [
	{ key: 'wife', value: 'Moglie' },
	{ key: 'son', value: 'Figlio' },
	{ key: 'daughter', value: 'Figlia' },
	{ key: 'father', value: 'Padre' },
	{ key: 'mother', value: 'Madre' }
];

const Checkout: FunctionComponent<Props> = (props) => {

	const { listing, profile, factory } = props;

	const [ cro, setCro ] = useState('');
	const [ receipt, setReceipt ] = useState<IFormFile>({
		data: '',
		name: '',
		mimeType: '',
		extension: '',
		isAttached: false
	});

	const { t } = useTranslation();
	const navigate = useStrictNavigate();

	usePreload([
		[ factory.api, () => props.notifyNetworkMainMemberFactoryRequested() ],
		[ listing.api, () => props.notifyNetworkFamilyListingRequested() ]
	]);

	if (!factory.data || isRequestFreshOrPending(listing.api)) return null;

	const members = [ ...listing.data.main ].concat(_.map(listing.data.externals, (x) => x.members).flat());

	return (
		<ResponsiveContainer>

			<Content>

				<Stepper
					steps={[
						{ current: false, completed: true, description: t('I tuoi dati'), route: getCardRequestFormRoute() },
						{ current: false, completed: true, description: t('I tuoi nuclei familiari'), route: getFamilyMembersRoute() },
						{ current: true, completed: false, description: t('Riepilogo e pagamento') }
					]}
					enableNext={!isEmpty(cro) && receipt.isAttached}
					onNext={() => navigate(getOrderConfirmationRoute())}
				/>

				<p>{t('Ricontrolla i dati inseriti e inserisci la ricevuta del pagamento.')}</p>

				<div className={style.membersContainer}>
					<div className={style.member}>
						<div className={style.detailsContainer}>
							<div className={style.detail}>
								{sprintf('%s %s', profile!.name, profile!.surname)}
							</div>
							<div className={style.detail}>
								<Tag classnames={'m-0'} title={t('Caponucleo')}/>
							</div>
							<div className={style.detail}/>
							<div className={mergeClasses(style.detail, style.price)}>1€</div>
						</div>
					</div>
					{
						members.map((member, index) => (
							<div key={index} className={style.member}>
								<div className={style.detailsContainer}>
									<div className={style.detail}>
										{sprintf('%s %s', member.name, member.surname)}
									</div>
									<div className={style.detail}>
										{_.find(RELATIONSHIP, x => x.key === member.relationship)?.value ?? '-'}
									</div>
									<div className={style.detail}>
										{!member.nucleoId || member.nucleoId === 1 ? t('Nucleo Familiare') : t('Nucleo Esterno')}
									</div>
									<div className={mergeClasses(style.detail, style.price)}>1€</div>
								</div>
							</div>
						))
					}
				</div>

				<div className={style.totalContainer}>
					<div className={style.guidelinesContainer}>
						<div className={'display-flex flex-row justify-start align-items-center'}>
							<ProImage src={InfoOutline}/>
							<p className={'ml-half'}>{t('Come si calcola il costo delle Carte Esercito?')}</p>
						</div>
						<div className={'display-flex flex-row justify-center align-items-center'}>
							<a
								className={style.priceGuidelines}
								href={'#'}
							>
								{t('Consulta direttiva costo Carta Esercito')}
							</a>
							<ProImage className={'pl-1 cursor-pointer'} src={ExternalLink}/>
						</div>
					</div>
					<div className={style.priceContainer}>
						{t('Tot.')} <h4 className={'ml-1'}>{(members.length + 1).toFixed(2)}€</h4>
					</div>
				</div>

				<div className={style.paymentContainer}>
					<h4>{t('Effettua pagamento')}</h4>
					<div className={'mt-1 display-flex flex-row justify-start align-items-center'}>
						<ProImage src={InfoOutline}/>
						<h6 className={'ml-half'}>{t('Come effettuo il pagamento?')}</h6>
					</div>
					<p>Effettua un bonifico a LOREM IPSUM DOLOR SIT AMET con CAUSALE “LOREM IPSUM SIT AMET” con l’ammontare del totale indicato. Salva
						la ricevuta come PDF e inseriscila qui sotto.</p>
					<div className={'mt-1 display-flex flex-row justify-start align-items-center'}>
						<Input
							name={'cro'}
							label={t('CRO')}
							value={cro}
							onChange={(e) => setCro(e.target.value)}
						/>
						<ProImage className={'ml-1 desktop-only'} src={Info}/>
					</div>
					<div className={'w-100 mt-1'}>
						<DocumentPicker
							title={t('Ricevuta bonifico')}
							subtitle={t('(peso massimo: 8Mb; formato: pdf)')}
							document={receipt}
							onFilePicked={(document) => setReceipt(document)}
							onClear={() => setReceipt({
								data: '',
								name: '',
								mimeType: '',
								extension: '',
								isAttached: false
							})}
						/>
					</div>
				</div>

			</Content>
		</ResponsiveContainer>
	);
};

export default Checkout;
