import { BasicStoreAction } from './models/actions/basicStoreAction';
import { StoreAction } from './models/actions/storeAction';

export const notifyAction = <T extends string>(type: T): BasicStoreAction<T> => {
	return { type };
};

export const notifyActionWithPayload = <T extends string, S>(type: T, payload: S): StoreAction<T, S> => {
	return { type, payload };
};
