import { call, fork, put, takeLatest } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import { IFamilyListingRequest, IFamilyListingResponse, INotifyNetworkFamilyListingRequested, NETWORK_FAMILY_LISTING_REQUESTED } from './types';
import { ConnectionCore } from '../../../../../network/defaultConnection';
import { isSuccessStatusCode } from '../../../../../network/utils';
import {
	notifyNetworkFamilyListingCompleted,
	notifyNetworkFamilyListingError,
	notifyNetworkFamilyListingFailed,
	notifyNetworkFamilyListingInit
} from './actions';
import delay from '../../../../../network/effects/delay';

const ENDPOINT = '/form/nuclei';

const api = ({ configuration }: IFamilyListingRequest) => {
	return ConnectionCore.getInstance().get<IFamilyListingResponse>(ENDPOINT);
};

function* entity(action: INotifyNetworkFamilyListingRequested) {
	try {

		yield put(notifyNetworkFamilyListingInit());

		yield delay(action.payload.configuration?.delay);
		const response: AxiosResponse<IFamilyListingResponse> = yield call(api, action.payload);

		if (isSuccessStatusCode(response.status)) {

			const data = response.data.data;

			yield put(notifyNetworkFamilyListingCompleted({
				status: response.status,
				message: response.data.message,
				data
			}));
		} else {
			yield put(notifyNetworkFamilyListingFailed({
				status: response.status,
				message: response.data.message,
				validationErrors: response.data.validation_errors
			}));
		}
	} catch (e) {
		console.log(`API call failed: ${ENDPOINT}`);
		console.error(e);
		yield put(notifyNetworkFamilyListingError());
	}
}

function* watcher() {
	yield takeLatest(NETWORK_FAMILY_LISTING_REQUESTED, entity);
}

export default fork(watcher);
