import { IApiRequest } from '../../../../../network/models/IApiRequest';
import { StoreAction } from '../../../../../store/models/actions/storeAction';
import { BasicStoreAction } from '../../../../../store/models/actions/basicStoreAction';
import { IApiPayload, Unsuccessful } from '../../../../../network/models/IApiPayload';
import { IApiResponse } from '../../../../../network/models/IApiResponse';

export type IMemberEditRequest = IApiRequest<{
	nucleoId: number | null;
	name: string;
	surname: string;
	birthDate: string;
	birthPlace: string;
	fiscalCode: string;
	nationality: string;
	primaryEmail: string;
	primaryPhone: string;
	relationship: string;
	isDefenseDepartmentEmployee: boolean;
	identityDocument: string;
	fiscalCodeDocument: string;
	passportPhotoDocument: string;
}>

export type IMemberEditResponse = IApiResponse

export type IMemberEditPayload = IApiPayload

export const NETWORK_MEMBER_EDIT_REQUESTED = 'NETWORK_MEMBER_EDIT_REQUESTED';
export const NETWORK_MEMBER_EDIT_INIT = 'NETWORK_MEMBER_EDIT_INIT';
export const NETWORK_MEMBER_EDIT_COMPLETED = 'NETWORK_MEMBER_EDIT_COMPLETED';
export const NETWORK_MEMBER_EDIT_FAILED = 'NETWORK_MEMBER_EDIT_FAILED';
export const NETWORK_MEMBER_EDIT_ERROR = 'NETWORK_MEMBER_EDIT_ERROR';
export const NETWORK_MEMBER_EDIT_CLEAR = 'NETWORK_MEMBER_EDIT_CLEAR';

export type INotifyNetworkMemberEditRequested = StoreAction<typeof NETWORK_MEMBER_EDIT_REQUESTED, IMemberEditRequest>
export type INotifyNetworkMemberEditInit = BasicStoreAction<typeof NETWORK_MEMBER_EDIT_INIT>
export type INotifyNetworkMemberEditCompleted = StoreAction<typeof NETWORK_MEMBER_EDIT_COMPLETED, IMemberEditPayload>
export type INotifyNetworkMemberEditFailed = StoreAction<typeof NETWORK_MEMBER_EDIT_FAILED, Unsuccessful<IMemberEditPayload>>
export type INotifyNetworkMemberEditError = BasicStoreAction<typeof NETWORK_MEMBER_EDIT_ERROR>
export type INotifyNetworkMemberEditClear = BasicStoreAction<typeof NETWORK_MEMBER_EDIT_CLEAR>

export type MemberEditNetworkActions =
	INotifyNetworkMemberEditRequested
	| INotifyNetworkMemberEditInit
	| INotifyNetworkMemberEditCompleted
	| INotifyNetworkMemberEditFailed
	| INotifyNetworkMemberEditError
	| INotifyNetworkMemberEditClear
	;
