import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IMemberEditPayload,
	IMemberEditRequest,
	INotifyNetworkMemberEditClear,
	INotifyNetworkMemberEditCompleted,
	INotifyNetworkMemberEditError,
	INotifyNetworkMemberEditFailed,
	INotifyNetworkMemberEditInit,
	INotifyNetworkMemberEditRequested,
	NETWORK_MEMBER_EDIT_CLEAR,
	NETWORK_MEMBER_EDIT_COMPLETED,
	NETWORK_MEMBER_EDIT_ERROR,
	NETWORK_MEMBER_EDIT_FAILED,
	NETWORK_MEMBER_EDIT_INIT,
	NETWORK_MEMBER_EDIT_REQUESTED
} from './types';

export const notifyNetworkMemberEditRequested = (data: IMemberEditRequest): INotifyNetworkMemberEditRequested => {
	return notifyActionWithPayload(NETWORK_MEMBER_EDIT_REQUESTED, data);
};

export const notifyNetworkMemberEditInit = (): INotifyNetworkMemberEditInit => {
	return notifyAction(NETWORK_MEMBER_EDIT_INIT);
};

export const notifyNetworkMemberEditCompleted = (data: IMemberEditPayload): INotifyNetworkMemberEditCompleted => {
	return notifyActionWithPayload(NETWORK_MEMBER_EDIT_COMPLETED, data);
};

export const notifyNetworkMemberEditFailed = (data: Unsuccessful<IMemberEditPayload>): INotifyNetworkMemberEditFailed => {
	return notifyActionWithPayload(NETWORK_MEMBER_EDIT_FAILED, data);
};

export const notifyNetworkMemberEditError = (): INotifyNetworkMemberEditError => {
	return notifyAction(NETWORK_MEMBER_EDIT_ERROR);
};

export const notifyNetworkMemberEditClear = (): INotifyNetworkMemberEditClear => {
	return notifyAction(NETWORK_MEMBER_EDIT_CLEAR);
};
