import FamilyMemberModalForm from './FamilyMemberModalForm';
import { connect } from 'react-redux';
import { RootState } from '../../../../store/rootReducer';
import { IMemberFormActionsProps, IMemberFormStateProps } from './store/props';
import { notifyNetworkMemberCreateClear, notifyNetworkMemberCreateRequested } from '../../network/member/create/actions';
import { notifyNetworkMemberEditClear, notifyNetworkMemberEditRequested } from '../../network/member/edit/actions';

const mapStateToProps = (state: RootState): IMemberFormStateProps => {
	return {
		create: state.cardRequestModule.member.create,
		profile: state.cardRequestModule.mainMember.profile,
		update: state.cardRequestModule.member.edit
	};
};

const mapDispatchToProps: IMemberFormActionsProps = {
	notifyNetworkMemberCreateRequested,
	notifyNetworkMemberCreateClear,
	notifyNetworkMemberEditRequested,
	notifyNetworkMemberEditClear
};

export default connect(mapStateToProps, mapDispatchToProps)(FamilyMemberModalForm);
