import { notifyAction, notifyActionWithPayload } from '../../../../../store/helpers';
import { Unsuccessful } from '../../../../../network/models/IApiPayload';
import {
	IMemberCreatePayload,
	IMemberCreateRequest,
	INotifyNetworkMemberCreateClear,
	INotifyNetworkMemberCreateCompleted,
	INotifyNetworkMemberCreateError,
	INotifyNetworkMemberCreateFailed,
	INotifyNetworkMemberCreateInit,
	INotifyNetworkMemberCreateRequested,
	NETWORK_MEMBER_CREATE_CLEAR,
	NETWORK_MEMBER_CREATE_COMPLETED,
	NETWORK_MEMBER_CREATE_ERROR,
	NETWORK_MEMBER_CREATE_FAILED,
	NETWORK_MEMBER_CREATE_INIT,
	NETWORK_MEMBER_CREATE_REQUESTED
} from './types';

export const notifyNetworkMemberCreateRequested = (data: IMemberCreateRequest): INotifyNetworkMemberCreateRequested => {
	return notifyActionWithPayload(NETWORK_MEMBER_CREATE_REQUESTED, data);
};

export const notifyNetworkMemberCreateInit = (): INotifyNetworkMemberCreateInit => {
	return notifyAction(NETWORK_MEMBER_CREATE_INIT);
};

export const notifyNetworkMemberCreateCompleted = (data: IMemberCreatePayload): INotifyNetworkMemberCreateCompleted => {
	return notifyActionWithPayload(NETWORK_MEMBER_CREATE_COMPLETED, data);
};

export const notifyNetworkMemberCreateFailed = (data: Unsuccessful<IMemberCreatePayload>): INotifyNetworkMemberCreateFailed => {
	return notifyActionWithPayload(NETWORK_MEMBER_CREATE_FAILED, data);
};

export const notifyNetworkMemberCreateError = (): INotifyNetworkMemberCreateError => {
	return notifyAction(NETWORK_MEMBER_CREATE_ERROR);
};

export const notifyNetworkMemberCreateClear = (): INotifyNetworkMemberCreateClear => {
	return notifyAction(NETWORK_MEMBER_CREATE_CLEAR);
};
