import { combineReducers } from 'redux';
import SystemModuleReducer from '../modules/system/SystemModuleReducer';
import CardRequestModuleReducer from '../modules/cardRequest/CardRequestModuleReducer';

const RootReducer = combineReducers({
	cardRequestModule: CardRequestModuleReducer,
	systemModule: SystemModuleReducer
});

export default RootReducer;

export type RootState = Readonly<ReturnType<typeof RootReducer>>;
