import { EnhanceActions } from '../../../../../store/utilityTypes';
import { IApiState } from '../../../../../network/models/IApiState';
import { IForm } from '../../../../../store/models/IForm';
import { StoreAction } from '../../../../../store/models/actions/storeAction';
import { DeepPartial } from '../../../../../types/deepPartial';
import { MainMemberFactoryNetworkActions } from '../../../network/mainMember/factory/types';
import { IMainMemberDetails, IMainMemberPersonalData } from '../../../models/IMainMember';
import { ShipmentType } from '../../../types/shipmentType';
import { IFormFile } from '../../../../../store/models/IFormFile';
import { IFormPicture } from '../../../../../store/models/IFormPicture';

export type IMainMemberFactoryForms = {
	secondaryEmail: string;
	secondaryPhone: string;
	shipment: ShipmentType;
	shipmentAddress: string;
	shipmentZipCode: string;
	shipmentCity: string;
	shipmentProvince: string;
	shipmentNotes: string;
	identityDocument: IFormFile;
	fiscalCodeDocument: IFormFile;
	passportPhotoDocument: IFormPicture;
};

export type IMainMemberFactoryState =
	IApiState<{ id: number; userId: string; caponucleo: IMainMemberPersonalData; caponucleoDettagli?: IMainMemberDetails; } | undefined>
	& IForm<IMainMemberFactoryForms>

export const MAIN_MEMBER_FACTORY_REFRESH = 'MAIN_MEMBER_FACTORY_REFRESH';
export const MAIN_MEMBER_FACTORY_UPDATE_FORM = 'MAIN_MEMBER_FACTORY_UPDATE_FORM';
export const MAIN_MEMBER_FACTORY_CLEAR_FORM = 'MAIN_MEMBER_FACTORY_CLEAR_FORM';

export type INotifyMainMemberFactoryRefresh = StoreAction<typeof MAIN_MEMBER_FACTORY_REFRESH, { id: number; userId: number; caponucleo: IMainMemberPersonalData; }>
export type INotifyMainMemberFactoryUpdateForm = StoreAction<typeof MAIN_MEMBER_FACTORY_UPDATE_FORM, DeepPartial<IMainMemberFactoryForms>>
export type INotifyMainMemberFactoryClearForm = StoreAction<typeof MAIN_MEMBER_FACTORY_CLEAR_FORM, DeepPartial<IMainMemberFactoryForms>>

export type MainMemberFactoryActions = EnhanceActions<never |
	MainMemberFactoryNetworkActions
	| INotifyMainMemberFactoryRefresh
	| INotifyMainMemberFactoryUpdateForm
	| INotifyMainMemberFactoryClearForm>
	;
