import { notifyActionWithPayload } from '../../../store/helpers';
import { INotifyRouterHistoryChanged, ROUTER_HISTORY_CHANGED } from './types';
import { HistoryAction } from '../../../types/historyAction';

export const notifyRouterHistoryChanged = (action: HistoryAction, location: string): INotifyRouterHistoryChanged => {
	return notifyActionWithPayload(ROUTER_HISTORY_CHANGED, { action, location });
};
