import React, { FunctionComponent } from 'react';
import { useLayoutMount } from '../../modules/system/hooks';
import { IRouterHistoryStoreProps } from './store/props';
import { DEVELOPMENT_MODE } from '../../constants';
import browserHistory from '../browserHistory';
import { HistoryAction } from '../../types/historyAction';
import { Location as HistoryLocation } from 'react-router-dom';

interface Props extends IRouterHistoryStoreProps {
	onHistoryChange?: (action: HistoryAction, location: HistoryLocation) => void;
}

const RouterHistory: FunctionComponent<Props> = (props) => {

	useLayoutMount(() => browserHistory.getHistory().listen(({ action, location }) => {

		if (DEVELOPMENT_MODE) {
			console.groupCollapsed('History');
			console.table({
				action,
				path: location.pathname,
				params: location.search
			});
			console.groupEnd();
		}

		// Notifying new location
		props.notifyRouterHistoryChanged(action, location.pathname);

		props.onHistoryChange && props.onHistoryChange(action, location);

	}));

	return null;
};

export default RouterHistory;
